import transformTextSearchOverview from './transformTextSearchOverview';

const handleTextSearchSucceeded = ({ state, updateState, action }) => {
    const {
        searchData,
        properties: { globalSearchDataConfigId, globalSearchViewConfigId },
    } = state;
    const nextSearchData = transformTextSearchOverview(
        searchData,
        action.payload,
        {
            globalSearchDataConfigId,
            globalSearchViewConfigId,
        }
    );

    updateState({
        ...state,
        searchData: nextSearchData,
    });
};

export default handleTextSearchSucceeded;
