import transformTabExactMatchResponse from './transformTabExactMatchResponse';

const handleTabExactMatchSucceededEffect = ({ state, updateState, action }) =>
    updateState({
        ...state,
        exactMatch: {
            isFetching: false,
            error: null,
            ...transformTabExactMatchResponse(action.payload),
        },
    });

export default handleTabExactMatchSucceededEffect;
