import React from 'react';
import PropTypes from 'prop-types';

import Record from './Record';

const ResultRecords = ({
    ariaLabelledBy,
    records,
    fieldOrder,
    onRecordSelect,
    section,
}) => (
    <ul className="sn-list-group" aria-labelledby={ariaLabelledBy}>
        {records.map((record, index) => (
            <Record
                key={record.sys_id || index}
                section={section}
                record={record}
                fields={fieldOrder}
                onSelect={onRecordSelect}
            />
        ))}
    </ul>
);

ResultRecords.propTypes = {
    ariaLabelledBy: PropTypes.string,
    fieldOrder: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
    section: PropTypes.string.isRequired,
    records: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
    onRecordSelect: PropTypes.func.isRequired,
};

ResultRecords.defaultProps = {
    ariaLabelledBy: undefined,
};

export default ResultRecords;
