import get from 'lodash/get';
import { t } from 'sn-translate';
import parseUserPreferenceValue from '../utils/parseUserPreferenceValue';
import RecentActionsModel from '../../../utils/RecentActionsModel';
import getBehaviorProp from '../../../behaviors/util/getBehaviorProp';
import { GLOBAL_SEARCH_CONFIG } from '../../../behaviors/constants';

const getGlobalConfigs = state => {
    if (state.workspaceConfigs) {
        return state.workspaceConfigs.viewConfig;
    }

    return getBehaviorProp(state, GLOBAL_SEARCH_CONFIG);
};

const handleTypeaheadUserPreferenceFetchSucceeded = ({
    state,
    updateState,
    action,
}) => {
    const { data } = action.payload;
    const userPreferencesQuery = get(data, 'GlideDomain_Query.user');

    const userPreferences = userPreferencesQuery
        ? {
              ...state.userPreferences,
              ...userPreferencesQuery.preferences[0],
          }
        : { ...state.userPreferences };

    const {
        recent_items_count: recentItemsCount,
        recent_terms_count: recentTermsCount,
        recent_ttl_in_days: recentTTLInDays,
    } = getGlobalConfigs(state);

    userPreferences.typeaheadRecentActionsModel = new RecentActionsModel(
        parseUserPreferenceValue(userPreferences),
        recentItemsCount,
        recentTermsCount,
        recentTTLInDays,
        t('Recently Searched'),
        t('Recently Viewed')
    );

    updateState({
        ...state,
        fetching: false,
        userPreferences,
    });
};

export default handleTypeaheadUserPreferenceFetchSucceeded;
