import { getProperty } from 'sn-uxpage-presource';
import {
    GLOBAL_SEARCH_ACTION,
    DRILLDOWN_PAGE_UPDATED,
} from '../componentWorkspaceGlobalSearchTab/actions';

export const KEY_CODES = {
    SPACE: 32,
    RETURN: 13,
    DOWN_ARROW: 40,
    UP_ARROW: 38,
    ESCAPE: 27,
};

class GlobalSearchUtils {
    static graphqlizeActionPayload(record) {
        const {
            sys_id: sysId,
            data,
            record_class_name: recordClassName,
            table,
        } = record;

        const graphqlizedRecord = {};
        if (data) {
            Object.keys(data).forEach(key => {
                graphqlizedRecord[key] = {
                    value: data[key].value,
                    displayValue: data[key].displayValue || data[key].display,
                    display: data[key].display,
                };
            });
        }
        return {
            table: recordClassName || table,
            sys_id: sysId,
            item: graphqlizedRecord,
        };
    }

    static multiplexedDispatch(
        dispatch,
        action = GLOBAL_SEARCH_ACTION,
        payload = {},
        type = {},
        metadata = {}
    ) {
        const pyld = { ...payload };
        // endOfPhase2Workaround for chrome integration
        // dispatch(action, payload, type);
        // workaround for seismic listen-to last-one-wins pattern
        pyld.action = action;
        pyld.actionMetadata = metadata;

        dispatch(GLOBAL_SEARCH_ACTION, pyld, type);
    }

    static dispatchItemSelected(dispatch, record, sourceId = '') {
        const { table, sys_id: sysId } = record;
        if (table && sysId) {
            GlobalSearchUtils.multiplexedDispatch(
                dispatch,
                'ITEM_SELECTED',
                GlobalSearchUtils.graphqlizeActionPayload(record),
                { type: 'ITEM_SELECTED' },
                { sourceId }
            );
        }
    }

    static dispatchTermSearched(dispatch, term) {
        if (term) {
            GlobalSearchUtils.multiplexedDispatch(
                dispatch,
                'TERM_SEARCHED',
                { term },
                { type: 'submit' }
            );
        }
    }

    static dispatchPageUpdate(dispatch, payload) {
        dispatch(DRILLDOWN_PAGE_UPDATED, payload);
    }

    static getSysPropertyAsNumber(name) {
        const value = getProperty(name);
        if (typeof value === 'number') {
            return value;
        }

        const parsedValue = parseInt(value, 10);

        // eslint-disable-next-line no-restricted-globals
        return isNaN(parsedValue) ? undefined : parsedValue;
    }
}

export default GlobalSearchUtils;
