import React from 'react';
import PropTypes from 'prop-types';
import map from 'lodash/map';
import get from 'lodash/get';
import { t } from 'sn-translate';
import { connectConfig } from '../../lib/sn-component-workspace-config';

import '@servicenow/now-badge';

const EXACT_MATCH_DATA = {
    sources: {
        EXACT_MATCH_SECTION: {
            name: t('Exact Match'),
            records: [1],
            databaseRecordCount: 1,
        },
    },
};

export class Sidebar extends React.PureComponent {
    renderSection(rawData, sysId, hideResultsCount) {
        const { onSidebarItemClick } = this.props;
        const data = get(rawData, ['sources', sysId]);
        if (!data) return null;
        if (data.databaseRecordCount < 1) return null;

        return (
            <li
                key={sysId}
                className="sn-list-group--item"
                onClick={e => onSidebarItemClick(e, sysId)}
                onKeyDown={e => onSidebarItemClick(e, sysId)}
                tabIndex={0}
            >
                <div className="sn-list-group--content">
                    <span>{data.name}</span>
                </div>
                {hideResultsCount ? null : (
                    <div className="sn-list-group--content snu-static">
                        <now-badge
                            round
                            size="md"
                            status="low"
                            value={data.databaseRecordCount}
                            variant="secondary"
                        />
                    </div>
                )}
            </li>
        );
    }

    render() {
        const {
            data,
            displayExactMatchSection,
            searchSources,
            hideResultsCount,
        } = this.props;

        const totalSources = get(data, 'sourceList.length', 0);
        const totalRecordCount = get(data, 'responseRecordCount', 0);

        if (!displayExactMatchSection) {
            if (totalSources === 0 || totalRecordCount === 0) {
                return null;
            }
        }

        return (
            <div className="sn-panel mod-global-search">
                <div className="sn-section">
                    <div>
                        <div className="sn-list-group sn-list-group__simple">
                            <ul className="sn-list-group">
                                {displayExactMatchSection &&
                                    this.renderSection(
                                        EXACT_MATCH_DATA,
                                        'EXACT_MATCH_SECTION',
                                        hideResultsCount
                                    )}
                                {map(searchSources, ({ sys_id: sysId }) =>
                                    this.renderSection(
                                        data,
                                        sysId,
                                        hideResultsCount
                                    )
                                )}
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

Sidebar.propTypes = {
    data: PropTypes.object, // eslint-disable-line react/forbid-prop-types
    searchSources: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
    onSidebarItemClick: PropTypes.func.isRequired,
    displayExactMatchSection: PropTypes.bool,
    hideResultsCount: PropTypes.bool,
};

Sidebar.defaultProps = {
    data: undefined,
    displayExactMatchSection: false,
    hideResultsCount: false,
};

function mapConfigToProps(config) {
    return {
        searchSources: config.search_sources,
    };
}

export default connectConfig(mapConfigToProps)(Sidebar);
