import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import FETCH_CONFIGURATIONS_QUERY from './constants';

import {
    FETCH_CONFIGURATIONS_SUCCEEDED,
    FETCH_CONFIGURATIONS_FAILED,
} from '../../actions';

const handleFetchConfigurationsEffect = createGraphQLEffect(
    FETCH_CONFIGURATIONS_QUERY,
    {
        variableList: ['globalSearchViewConfigId'],
        successActionType: FETCH_CONFIGURATIONS_SUCCEEDED,
        errorActionType: FETCH_CONFIGURATIONS_FAILED,
    }
);

export default handleFetchConfigurationsEffect;
