import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
    TYPEAHEAD_ITEM_LOOKUP_STARTED,
    TYPEAHEAD_ITEM_LOOKUP_SUCCEEDED,
    TYPEAHEAD_ITEM_LOOKUP_FAILED,
} from '../actions';

const handleTypeaheadItemLookup = createHttpEffect(
    '/api/now/v1/search/itemlookup',
    {
        method: 'GET',
        headers: {},
        queryParams: ['sysparm_table_name', 'sysparm_item_sys_id'],
        startActionType: TYPEAHEAD_ITEM_LOOKUP_STARTED,
        successActionType: TYPEAHEAD_ITEM_LOOKUP_SUCCEEDED,
        errorActionType: TYPEAHEAD_ITEM_LOOKUP_FAILED,
    }
);

export default handleTypeaheadItemLookup;
