import { getBehaviorProp } from 'sn-uxpage-presource';
import head from 'lodash/head';
import isEmpty from 'lodash/isEmpty';
import transforms from '../transformMaps';

const _getBehaviorProp = (state, name) => {
    const val = getBehaviorProp(state, name);
    const map = head(transforms.filter(t => t.behaviorName === name)) || {};
    const { transform } = map;

    if (transform && !isEmpty(val)) {
        return transform(val);
    }

    return val;
};

export default _getBehaviorProp;
