import { createPreSourceBehavior } from 'sn-uxpage-presource';

import { PRESOURCE_COMPONENT } from '../constants';

/**
 * Creates a map of Prefetched Resource Behaviors
 *
 * @param {object[]} transforms - An array of objects containing a behavior name and the
 * transform function to use for the created presource behavior
 */
export default transforms => {
    const behaviorMap = transforms.reduce(
        (prev, curr) => ({
            ...prev,
            [curr.behaviorName]: createPreSourceBehavior(PRESOURCE_COMPONENT, {
                name: curr.behaviorName,
            }),
        }),
        {}
    );
    return behaviorMap;
};
