import { FETCH_CONFIGURATIONS } from '../../actions';

const handlePropertyChangedEffect = ({
    state,
    dispatch,
    action,
    updateState,
}) => {
    const {
        payload: { name },
    } = action;

    const {
        properties: { globalSearchDataConfigId, globalSearchViewConfigId },
    } = state;

    if (
        name === 'globalSearchViewConfigId' ||
        name === 'globalSearchDataConfigId'
    ) {
        if (globalSearchDataConfigId && globalSearchViewConfigId) {
            dispatch(FETCH_CONFIGURATIONS, {
                globalSearchDataConfigId,
                globalSearchViewConfigId,
            });

            updateState({
                ...state,
                workspaceConfigs: null,
                searchData: null,
            });
        }
    }
};

export default handlePropertyChangedEffect;
