import { style as iconStyle } from '@servicenow/now-legacy-icon';
import merge from 'lodash/merge';
import noop from 'lodash/noop';
import omit from 'lodash/omit';
import PropTypes from 'prop-types';
import React from 'react';
import getBehaviorProp from '../behaviors/util/getBehaviorProp';
import {
    GLOBAL_SEARCH_CONFIG,
    SEARCH_SOURCE,
    USER_PREFERENCE,
} from '../behaviors/constants';
import { ConfigProvider } from '../lib/sn-component-workspace-config';
import createLegacyForceRender from '../utils/createLegacyForceRender';
import ErrorBoundary from '../utils/ErrorBoundary';
import GlobalSearchUtils, { KEY_CODES } from '../utils/GlobalSearchUtils';
import PresourceDefaults from '../utils/PresourceDefaults';
import SearchSourceErrors from '../utils/SearchSourceErrors';
import SearchTabDrillDown from './ComponentWorkspaceGlobalSearchTabDrillDown';
import SearchTabOverview from './ComponentWorkspaceGlobalSearchTabOverview';
import FetchConfigLoadingView from './GlobalSearchResult/FetchConfigLoadingView';
import { ENUM_TAB_MODES } from './constants';
import { exactMatchDataShape } from './propTypes';
import style from './_componentWorkspaceGlobalSearchTab.scss';
import NoResults from './GlobalSearchResult/NoResults';

const getConfigFromSysProperties = () => {
    const globalSearchConfig = {};
    const paginationDebounce = GlobalSearchUtils.getSysPropertyAsNumber(
        'com.snc.agent_workspace.global_search.tab.pagination_debounce'
    );
    if (paginationDebounce) {
        globalSearchConfig.tab_pagination_debounce = paginationDebounce;
    }
    return { global_search_config: globalSearchConfig };
};

const getGlobalSearchConfigPresourceData = props =>
    getBehaviorProp(props, GLOBAL_SEARCH_CONFIG);
const getSearchSourcePresourceData = props =>
    getBehaviorProp(props, SEARCH_SOURCE);
const getUserPreferencePresourceData = props =>
    getBehaviorProp(props, USER_PREFERENCE);

const getResolvedConfigurations = (
    isLegacyWorkspace,
    props,
    workspaceConfigs
) => {
    const {
        properties: {
            globalSearchViewConfigId,
            globalSearchDataConfigId,
            hideSearchResultsCount,
        },
    } = props;
    const snSearchSources = isLegacyWorkspace
        ? getSearchSourcePresourceData(props)
        : workspaceConfigs.dataConfig;
    const snUserPreferences = isLegacyWorkspace
        ? getUserPreferencePresourceData(props)
        : workspaceConfigs.exactMatchPreference;
    const snGlobalSearchConfig = isLegacyWorkspace
        ? getGlobalSearchConfigPresourceData(props)
        : workspaceConfigs.viewConfig;

    snGlobalSearchConfig.hide_search_results_count = isLegacyWorkspace
        ? snGlobalSearchConfig.hide_search_results_count
        : hideSearchResultsCount;

    const metadata = {
        viewSysId: isLegacyWorkspace ? null : globalSearchViewConfigId,
        dataSysId: isLegacyWorkspace ? null : globalSearchDataConfigId,
    };

    return {
        snSearchSources,
        snUserPreferences,
        snGlobalSearchConfig,
        metadata,
    };
};

const DEFAULT_CONFIG = {
    global_search_config: PresourceDefaults[GLOBAL_SEARCH_CONFIG],
    search_sources: [],
    metadata: {},
};

class ComponentWorkspaceGlobalSearchTab extends React.Component {
    static get MODES() {
        return ENUM_TAB_MODES;
    }

    state = {
        mode: this.constructor.MODES.OVERVIEW,
        drilledDownSection: '',
        drilledDownSectionTitle: '',
        scrollIntoViewSection: '',
    };

    constructor(props) {
        super(props);

        this.onRecordSelect = this.onRecordSelect.bind(this);
        this.onSectionDrillDown = this.onSectionDrillDown.bind(this);
        this.handleBackToResultsClick = this.handleBackToResultsClick.bind(
            this
        );
        this.setForceRenderTimestamp = this.setForceRenderTimestamp.bind(this);
        this.forceLegacyRender = createLegacyForceRender(
            this.setForceRenderTimestamp
        );
        this.resetOverviewState = this.resetOverviewState.bind(this);
        this.handleSidebarItemClick = this.handleSidebarItemClick.bind(this);
    }

    componentDidMount() {
        this.forceLegacyRender(noop);
    }

    onRecordSelect(record, sourceId) {
        const {
            helpers: { dispatch },
        } = this.props;
        const { table } = record;
        if (table) {
            GlobalSearchUtils.dispatchItemSelected(dispatch, record, sourceId);
        }
    }

    onSectionDrillDown(searchSourceId, searchSourceTitle) {
        this.setState({
            mode: this.constructor.MODES.DRILL_DOWN,
            drilledDownSection: searchSourceId,
            drilledDownSectionTitle: searchSourceTitle,
            scrollIntoViewSection: '',
        });
    }

    get config() {
        const { properties, workspaceConfigs } = this.props;
        const { isLegacyWorkspace } = properties;

        // if going to use gql data, wait for response back
        if (!isLegacyWorkspace && !workspaceConfigs) {
            return null;
        }

        const {
            snSearchSources,
            snGlobalSearchConfig,
            snUserPreferences,
            metadata,
        } = getResolvedConfigurations(
            isLegacyWorkspace,
            this.props,
            workspaceConfigs
        );

        const configFromSysProperties = getConfigFromSysProperties();
        const config = {
            global_search_config: {
                ...snGlobalSearchConfig,
                ...snUserPreferences,
            },
            search_sources: snSearchSources,
            metadata,
        };

        return merge({}, DEFAULT_CONFIG, config, configFromSysProperties);
    }

    setForceRenderTimestamp(timestamp) {
        // eslint-disable-next-line react/no-unused-state
        this.setState({ forceRender: timestamp });
    }

    handleBackToResultsClick() {
        this.setState({ mode: this.constructor.MODES.OVERVIEW });
    }

    handleSidebarItemClick(event, section) {
        if (event.which === KEY_CODES.RETURN || event.type === 'click')
            this.setState({ scrollIntoViewSection: section });
    }

    resetOverviewState() {
        this.setState({
            scrollIntoViewSection: '',
            mode: ENUM_TAB_MODES.OVERVIEW,
            drilledDownSection: '',
        });
    }

    render() {
        const {
            searchData,
            searchErrors,
            exactMatch,
            properties: { term, isLegacyWorkspace },
            page,
            isFetchingWorkspaceConfig,
        } = this.props;
        const {
            mode,
            scrollIntoViewSection,
            drilledDownSection,
            drilledDownSectionTitle,
        } = this.state;

        const otherSeismicProps = omit(this.props, ['channels']);
        const seismicProps = { ...otherSeismicProps };
        const trimmedTerm = term ? term.trim() : '';
        const configLoaded = this.config !== null;

        if (!term) {
            return <NoResults term="" />;
        }

        // Display a loading view while configurations are being fetched
        if (
            isFetchingWorkspaceConfig ||
            (!isLegacyWorkspace && !configLoaded && term)
        ) {
            return (
                <ConfigProvider config={DEFAULT_CONFIG}>
                    <FetchConfigLoadingView term={term} />
                </ConfigProvider>
            );
        }

        return (
            <ConfigProvider config={this.config}>
                <div className="sn-panel tab-container">
                    <ErrorBoundary>
                        <SearchTabOverview
                            searchData={searchData}
                            searchErrors={searchErrors}
                            exactMatchData={exactMatch}
                            {...seismicProps}
                            term={trimmedTerm}
                            onRecordSelect={this.onRecordSelect}
                            onSectionDrillDown={this.onSectionDrillDown}
                            resetOverviewState={this.resetOverviewState}
                            handleSidebarItemClick={this.handleSidebarItemClick}
                            scrollIntoViewSection={scrollIntoViewSection}
                            mode={mode}
                        />
                    </ErrorBoundary>
                    <div
                        className={`sn-panel tab-container${
                            mode !== ENUM_TAB_MODES.DRILL_DOWN ? ' hidden' : ''
                        }`}
                    >
                        {mode === ENUM_TAB_MODES.DRILL_DOWN && (
                            <ErrorBoundary>
                                <SearchTabDrillDown
                                    drilledDownSection={drilledDownSection}
                                    drilledDownSectionTitle={
                                        drilledDownSectionTitle
                                    }
                                    handleBackToResultsClick={
                                        this.handleBackToResultsClick
                                    }
                                    {...seismicProps}
                                    term={trimmedTerm}
                                    page={page || null}
                                    onRecordSelect={this.onRecordSelect}
                                />
                            </ErrorBoundary>
                        )}
                    </div>
                </div>
            </ConfigProvider>
        );
    }
}

ComponentWorkspaceGlobalSearchTab.propTypes = {
    exactMatch: exactMatchDataShape,
    properties: PropTypes.shape({
        term: PropTypes.string,
        instanceId: PropTypes.string,
        globalSearchDataConfigId: PropTypes.string,
        globalSearchViewConfigId: PropTypes.string,
        isLegacyWorkspace: PropTypes.bool,
    }).isRequired,
    searchData: PropTypes.shape({
        term: PropTypes.string,
        responseRecordCount: PropTypes.number,
        sourceList: PropTypes.array,
        sources: PropTypes.object,
    }),
    searchErrors: PropTypes.shape({
        errors: PropTypes.array,
    }),
    helpers: PropTypes.func.isRequired,
    page: PropTypes.number,
    workspaceConfigs: PropTypes.shape({
        viewConfig: PropTypes.object,
        dataConfig: PropTypes.array,
        exactMatchPreference: PropTypes.object,
    }),
    hideSearchResultsCount: PropTypes.bool,
    isFetchingWorkspaceConfig: PropTypes.bool,
};

ComponentWorkspaceGlobalSearchTab.defaultProps = {
    exactMatch: undefined,
    searchData: undefined,
    searchErrors: new SearchSourceErrors(),
    page: 1,
    workspaceConfigs: undefined,
    hideSearchResultsCount: false,
    isFetchingWorkspaceConfig: false,
};

ComponentWorkspaceGlobalSearchTab.style = `${style} ${iconStyle}`;

export default ComponentWorkspaceGlobalSearchTab;
