import React from 'react';
import PropTypes from 'prop-types';

const SnPopover = ({ className, children, ...props }) => (
    <div className={`sn-popover ${className}`} {...props}>
        {children}
    </div>
);

SnPopover.defaultProps = {
    className: '',
};

SnPopover.propTypes = {
    className: PropTypes.string,
    children: PropTypes.node.isRequired,
};

export default SnPopover;
