import '@servicenow/now-icon';
import PropTypes from 'prop-types';
import React from 'react';
import { KEY_CODES } from '../../utils/GlobalSearchUtils';

class Icon extends React.PureComponent {
    constructor(props) {
        super(props);

        this.iconOnClick = this.iconOnClick.bind(this);
    }

    iconOnClick(event) {
        const { onClick } = this.props;
        if (
            typeof event.which === 'undefined' ||
            (event.which &&
                (event.which === KEY_CODES.RETURN ||
                    event.which === KEY_CODES.SPACE))
        ) {
            onClick(event);
        }
    }

    render() {
        const {
            iconClassName,
            iconSize,
            buttonClassName = '',
            ariaLabel,
            ariaExpanded,
            setRef,
            tabIndex,
        } = this.props;

        return (
            <button
                type="button"
                aria-label={ariaLabel}
                aria-expanded={ariaExpanded}
                aria-controls="sncwsgs-typeahead-input"
                onClick={this.iconOnClick}
                onKeyDown={this.iconOnClick}
                className={`${buttonClassName ? ` ${buttonClassName}` : ''}`}
                ref={ref => {
                    setRef(ref);
                }}
                tabIndex={tabIndex}
            >
                <now-icon icon={iconClassName} size={iconSize} />
            </button>
        );
    }
}

Icon.propTypes = {
    ariaExpanded: PropTypes.bool,
    iconClassName: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired,
    iconSize: PropTypes.string,
    buttonClassName: PropTypes.string,
    ariaLabel: PropTypes.string,
    setRef: PropTypes.func,
    tabIndex: PropTypes.number,
};

Icon.defaultProps = {
    ariaLabel: '',
    ariaExpanded: undefined,
    buttonClassName: undefined,
    iconSize: 'sm',
    tabIndex: -1,
    setRef: () => undefined,
};

export default Icon;
