import { ENUM_TYPEAHEAD_RECORD_TYPES } from '../TypeaheadPopover/TypeaheadRecord';
import RecentActionsModel from '../../utils/RecentActionsModel';

export const NAVIGATION_DIRECTIONS = {
    UP: 'up',
    DOWN: 'down',
};

class NavigationWrapper {
    static get DIRECTIONS() {
        return NAVIGATION_DIRECTIONS;
    }

    static getUpdatedIndex(dataModel, index, type) {
        if (dataModel.size === 0) return undefined;

        if (type === NavigationWrapper.DIRECTIONS.UP) {
            let previousRecord = (index - 1) % dataModel.size;
            if (index === 0) {
                previousRecord = -1;
            } else if (index === -1) {
                previousRecord = dataModel.size - 1;
            }
            return previousRecord;
        }
        if (type === NavigationWrapper.DIRECTIONS.DOWN) {
            let nextRecord = (index + 1) % dataModel.size;
            if (index === dataModel.size - 1) {
                nextRecord = -1;
            }
            return nextRecord;
        }
        return undefined;
    }

    static getRecordBySectionAndRecordIndex(
        dataModel,
        sectionIndex,
        recordIndex
    ) {
        const sectionId =
            dataModel.sectionsFilteredByResponseCount[sectionIndex];
        const records = dataModel.getSectionRecordsBySectionId(sectionId);
        const record = records[recordIndex];
        return record;
    }

    static getRecord(dataModel, flattenedIndex = -1) {
        if (flattenedIndex === -1) {
            return {};
        }
        const {
            sectionIndex,
            recordIndex,
        } = NavigationWrapper.getSectionAndRecordIndices(
            dataModel,
            flattenedIndex
        );

        if (dataModel instanceof RecentActionsModel) {
            const record = NavigationWrapper.getRecordBySectionAndRecordIndex(
                dataModel,
                sectionIndex,
                recordIndex
            );
            if (typeof record === 'string') {
                return { type: ENUM_TYPEAHEAD_RECORD_TYPES.TERM, record };
            }
            if (typeof record === 'object') {
                return { type: ENUM_TYPEAHEAD_RECORD_TYPES.RESULT, record };
            }
        } else {
            if (
                sectionIndex ===
                dataModel.filteredSectionRecordLengths.length - 1
            ) {
                return { type: ENUM_TYPEAHEAD_RECORD_TYPES.SEARCH_ALL_ACTION };
            }
            const record = NavigationWrapper.getRecordBySectionAndRecordIndex(
                dataModel,
                sectionIndex,
                recordIndex
            );
            return { type: ENUM_TYPEAHEAD_RECORD_TYPES.RESULT, record };
        }
        return {};
    }

    static getSectionAndRecordIndices(dataModel, flattenedIndex = -1) {
        let sectionIndex = -1;
        let recordIndex = -1;

        if (flattenedIndex === -1) {
            return { sectionIndex, recordIndex };
        }
        let count = 0;
        let lastCount = 0;
        sectionIndex = 0;
        for (
            let i = 0;
            i < dataModel.filteredSectionRecordLengths.length;
            i += 1
        ) {
            count += dataModel.filteredSectionRecordLengths[i];
            if (flattenedIndex < count) {
                sectionIndex = i;
                recordIndex = flattenedIndex - lastCount;
                break;
            }
            lastCount += dataModel.filteredSectionRecordLengths[i];
        }
        return { sectionIndex, recordIndex };
    }

    static getFlattenedIndex(dataModel, sectionIndex, recordIndex) {
        let flattenedIndex = 0;
        for (let i = 0; i < sectionIndex; i += 1) {
            flattenedIndex += dataModel.filteredSectionRecordLengths[i];
        }
        flattenedIndex += recordIndex;
        return flattenedIndex;
    }
}

export default NavigationWrapper;
