import React from 'react';
import PropTypes from 'prop-types';
import { t } from 'sn-translate';

import get from 'lodash/get';
import TabHeader from './Tab/TabHeader';
import TabBody from './Tab/TabBody';
import TabContainer from './Tab/TabContainer';
import ResultRecords from './ResultRecords';
import { connectConfig } from '../../lib/sn-component-workspace-config';

import '@servicenow/now-button';
import '@servicenow/now-pagination';

export class DrillDownView extends React.PureComponent {
    constructor(props) {
        super(props);
        this.nodeToScroll = null;
        this.onBackToAllResultsClick = this.onBackToAllResultsClick.bind(this);
        this.getNodeToScrollRef = this.getNodeToScrollRef.bind(this);
    }

    componentDidUpdate() {
        const { data } = this.props;
        if (data && data.isFetching && this.nodeToScroll)
            this.nodeToScroll.scrollTop = 0;
    }

    onBackToAllResultsClick(event) {
        const { resetDrillDownState } = this.props;
        event.preventDefault();
        resetDrillDownState(event);
    }

    getNodeToScrollRef(el) {
        this.nodeToScroll = el;
    }

    render() {
        const {
            data,
            paginationLimit,
            currentPage,
            onRecordSelect,
            term,
            sectionId,
            sectionTitle,
            hideResultsCount,
        } = this.props;

        const responseRecordCount = get(data, 'responseRecordCount', 0);
        const visibleCount = get(data, 'sourceData.records.length', 0);
        const databaseRecordCount = get(
            data,
            'sourceData.databaseRecordCount',
            0
        );
        const displayFields = get(data, 'sourceData.displayFields', []);
        const records = get(data, 'sourceData.records', []);
        const tableName = get(data, 'sourceData.tableName');
        const isFetching = data && data.isFetching;

        const paginationProps = {
            hideLimitSelector: true,
            limit: paginationLimit,
            page: currentPage,
            recordCount: responseRecordCount,
            visibleCount,
        };

        let headerTitle = '';
        if (isFetching) {
            headerTitle = t('Searching for "{0}" in {1}', term, sectionTitle);
        } else if (hideResultsCount) {
            headerTitle = t('Results for "{0}" in {1}', term, sectionTitle);
        } else {
            headerTitle = t(
                '{0} results for "{1}" in {2}',
                responseRecordCount.toLocaleString(),
                term,
                sectionTitle
            );
        }

        return (
            <React.Fragment>
                <TabContainer className="tab-drilldown-container">
                    <div className="sn-panel--row sn-panel--row__static back-button">
                        <a
                            className="sn-url"
                            onClick={this.onBackToAllResultsClick}
                            href="javascript:void(0)" // eslint-disable-line no-script-url
                        >
                            <now-button
                                bare
                                icon-name="arrow-left-outline"
                                label={t('Back')}
                                size="md"
                                variant="tertiary"
                            />
                        </a>
                    </div>
                    <div
                        className="global-search-container"
                        ref={this.getNodeToScrollRef}
                    >
                        <TabHeader
                            title={headerTitle}
                            spinnerEnabled={!!isFetching}
                        />
                        <TabBody>
                            {databaseRecordCount > 0 && (
                                <ResultRecords
                                    fieldOrder={displayFields}
                                    sectionTableName={tableName}
                                    section={sectionId}
                                    records={records}
                                    onRecordSelect={onRecordSelect}
                                />
                            )}
                        </TabBody>
                    </div>
                </TabContainer>
                <now-pagination
                    {...paginationProps}
                    component-id="drill-down-pagination"
                    hideRange={hideResultsCount}
                    hideRowCount={hideResultsCount}
                />
            </React.Fragment>
        );
    }
}

DrillDownView.propTypes = {
    data: PropTypes.object, // eslint-disable-line react/forbid-prop-types
    paginationLimit: PropTypes.number.isRequired,
    currentPage: PropTypes.number.isRequired,
    onRecordSelect: PropTypes.func.isRequired,
    resetDrillDownState: PropTypes.func.isRequired,
    term: PropTypes.string.isRequired,
    sectionId: PropTypes.string.isRequired,
    sectionTitle: PropTypes.string.isRequired,
    hideResultsCount: PropTypes.bool,
};

DrillDownView.defaultProps = {
    data: undefined,
    hideResultsCount: false,
};

function mapConfigToProps(config) {
    return {
        paginationLimit:
            config.global_search_config.tab_drilldown_items_per_page,
        hideResultsCount: config.global_search_config.hide_search_results_count,
    };
}

export default connectConfig(mapConfigToProps)(DrillDownView);
