import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
    WORKSPACE_TEXT_SEARCH_STARTED,
    WORKSPACE_TEXT_SEARCH_SUCCEEDED,
    WORKSPACE_TEXT_SEARCH_FAILED,
} from '../../actions';

const handleTextSearch = createHttpEffect(
    '/api/now/v1/search/sources/:sys_id/textsearch',
    {
        batch: false,
        pathParams: ['sys_id'],
        queryParams: ['sysparm_limit', 'sysparm_page', 'sysparm_term'],
        startActionType: WORKSPACE_TEXT_SEARCH_STARTED,
        successActionType: WORKSPACE_TEXT_SEARCH_SUCCEEDED,
        errorActionType: WORKSPACE_TEXT_SEARCH_FAILED,
    }
);

export default handleTextSearch;
