import { actionTypes } from '@servicenow/ui-core';
import {
    EXACT_MATCH_FAILED,
    EXACT_MATCH_FETCH,
    EXACT_MATCH_STARTED,
    EXACT_MATCH_SUCCEEDED,
    USER_PREFERENCES_FETCH,
    USER_PREFERENCES_FETCH_FAILED,
    USER_PREFERENCES_FETCH_STARTED,
    USER_PREFERENCES_FETCH_SUCCEEDED,
    USER_PREFERENCES_SET_FAILED,
    USER_PREFERENCES_SET_STARTED,
    USER_PREFERENCES_SET_SUCCEEDED,
    GLOBAL_SEARCH_ACTION,
    USER_PREFERENCES_SET,
    RECENT_ACTIONS_MODEL_SET,
    FETCH_CONFIGURATIONS,
    FETCH_CONFIGURATIONS_SUCCEEDED,
    FETCH_CONFIGURATIONS_FAILED,
    FIT_RESIZE,
} from './actions';
import {
    handleTypeaheadExactMatchFetch,
    handleTypeaheadExactMatchFetchStarted,
    handleTypeaheadExactMatchFetchSucceeded,
    handleTypeaheadExactMatchFetchFailed,
    handleTypeaheadUserPreferenceFetch,
    handleTypeaheadUserPreferenceFetchFailed,
    handleTypeaheadUserPreferenceFetchStarted,
    handleTypeaheadUserPreferenceFetchSucceeded,
    handleTypeaheadUserPreferenceSet,
    handleTypeaheadUserPreferenceSetStarted,
    handleTypeaheadUserPreferenceSetSucceeded,
    handleTypeaheadUserPreferenceSetFailed,
    handleBootstrappedEffect,
    handleGlobalSearchAction,
    handleRecentActionsModelSet,
    handleFetchConfigurationsEffect,
    handleFetchConfigurationsSucceededEffect,
    handleFetchConfigurationsFailedEffect,
    handleFitResize,
    handlePropertyChangedEffect,
} from './effects';
import effectProxy from '../utils/effectProxy';

const {
    COMPONENT_BOOTSTRAPPED,
    COMPONENT_CONNECTED,
    COMPONENT_DISCONNECTED,
    COMPONENT_RENDERED,
    COMPONENT_PROPERTY_CHANGED,
} = actionTypes;

export default {
    [COMPONENT_CONNECTED]({ dispatch }) {
        window.addEventListener('resize', () => {
            dispatch(FIT_RESIZE);
        });
    },
    [COMPONENT_DISCONNECTED]() {
        window.removeEventListener('resize', () => {});
    },
    [COMPONENT_RENDERED]({ dispatch }) {
        dispatch(FIT_RESIZE);
    },
    [EXACT_MATCH_FETCH]: {
        ...handleTypeaheadExactMatchFetch,
        stopPropagation: true,
    },
    [EXACT_MATCH_STARTED]: {
        effect: handleTypeaheadExactMatchFetchStarted,
        stopPropagation: true,
    },
    [EXACT_MATCH_SUCCEEDED]: {
        effect: handleTypeaheadExactMatchFetchSucceeded,
        stopPropagation: true,
    },
    [EXACT_MATCH_FAILED]: {
        effect: handleTypeaheadExactMatchFetchFailed,
        stopPropagation: true,
    },
    [RECENT_ACTIONS_MODEL_SET]: {
        effect: handleRecentActionsModelSet,
        stopPropagation: true,
    },
    [USER_PREFERENCES_FETCH]: effectProxy(handleTypeaheadUserPreferenceFetch),
    [USER_PREFERENCES_FETCH_STARTED]: {
        effect: handleTypeaheadUserPreferenceFetchStarted,
        stopPropagation: true,
    },
    [USER_PREFERENCES_FETCH_SUCCEEDED]: {
        effect: handleTypeaheadUserPreferenceFetchSucceeded,
        stopPropagation: true,
    },
    [USER_PREFERENCES_FETCH_FAILED]: {
        effect: handleTypeaheadUserPreferenceFetchFailed,
        stopPropagation: true,
    },
    [USER_PREFERENCES_SET]: {
        ...handleTypeaheadUserPreferenceSet,
        stopPropagation: true,
    },
    [USER_PREFERENCES_SET_STARTED]: {
        effect: handleTypeaheadUserPreferenceSetStarted,
        stopPropagation: true,
    },
    [USER_PREFERENCES_SET_SUCCEEDED]: {
        effect: handleTypeaheadUserPreferenceSetSucceeded,
        stopPropagation: true,
    },
    [USER_PREFERENCES_SET_FAILED]: {
        effect: handleTypeaheadUserPreferenceSetFailed,
        stopPropagation: true,
    },
    [FETCH_CONFIGURATIONS]: effectProxy(handleFetchConfigurationsEffect),
    [FETCH_CONFIGURATIONS_SUCCEEDED]: {
        effect: handleFetchConfigurationsSucceededEffect,
        stopPropagation: true,
    },
    [FETCH_CONFIGURATIONS_FAILED]: {
        effect: handleFetchConfigurationsFailedEffect,
        stopPropagation: true,
    },
    [COMPONENT_BOOTSTRAPPED]: handleBootstrappedEffect,
    [GLOBAL_SEARCH_ACTION]: handleGlobalSearchAction,
    [FIT_RESIZE]: {
        effect: handleFitResize,
        stopPropagation: true,
    },
    [COMPONENT_PROPERTY_CHANGED]: handlePropertyChangedEffect,
};
