import {
    USER_PREFERENCES_FETCH,
    ITEM_SELECTED,
} from '../../../componentWorkspaceGlobalSearchTypeahead/actions';

import { SN_WS_SEARCH_TAB_RECORD_CLICKED } from '../../actions';

const handleGlobalSearchActionEffect = ({ action, dispatch }) => {
    if (action.payload.action === ITEM_SELECTED) {
        const {
            item,
            table,
            sys_id: sysId,
            actionMetadata: { sourceId },
        } = action.payload;
        const recordData = { data: item, table, sys_id: sysId };

        dispatch(SN_WS_SEARCH_TAB_RECORD_CLICKED, {
            table,
            sysId,
        });

        dispatch(USER_PREFERENCES_FETCH, {
            data: recordData,
            name: 'workspace.globalSearch.recentActions',
            sourceId,
        });
    }
};

export default handleGlobalSearchActionEffect;
