import {
    globalSearchConfigTransform,
    searchSourcesTransform,
    userPreferencesTransform,
} from '../../../behaviors/transforms';

const handleFetchConfigurationsSucceeded = ({ state, action, updateState }) => {
    const { payload } = action;
    const {
        properties: { hideSearchResultsCount },
    } = state;
    const viewConfig = globalSearchConfigTransform(
        {
            ...payload,
            props: {
                hide_search_results_count: { value: hideSearchResultsCount },
            },
        },
        false
    );
    const dataConfig = searchSourcesTransform(payload, false);
    const exactMatchPreference = userPreferencesTransform(payload);

    updateState({
        ...state,
        workspaceConfigs: {
            viewConfig,
            dataConfig,
            exactMatchPreference,
        },
        isFetchingWorkspaceConfig: false,
    });
};

export default handleFetchConfigurationsSucceeded;
