import React from 'react';
import PropTypes from 'prop-types';

import Overview from './Overview';

/**
 * A simple wrapper around overview meant to always display a loading state
 */
export class FetchConfigLoadingView extends React.PureComponent {
    render() {
        const { term } = this.props;
        return (
            <Overview
                term={term}
                scrollIntoViewSection=""
                reading
                isOverviewVisible
                data={null}
                onSidebarItemClick={() => {}}
                onRecordSelect={() => {}}
                onSectionDrillDown={() => {}}
                exactMatchData={null}
                hideResultsCount={false}
            />
        );
    }
}

FetchConfigLoadingView.propTypes = {
    term: PropTypes.string.isRequired,
};

export default FetchConfigLoadingView;
