import CompanyCard from './CompanyCard';
import LocationCard from './LocationCard';
import UserCard from './UserCard';
import DefaultCard from './DefaultCard';

/**
 * Acts as a higher order component that returns the right card component
 */

export default function getCardComponent(tableName) {
    switch (tableName) {
        case 'core_company':
            return CompanyCard;
        case 'cmn_location':
            return LocationCard;
        case 'sys_user':
            return UserCard;
        default:
            return DefaultCard;
    }
}
