const shouldAddError = (searchErrors, nextError) =>
    !searchErrors.errors.filter(
        error =>
            error.searchTerm === nextError.searchTerm &&
            error.sysId === nextError.sysId
    ).length;

const toSearchError = action => ({
    ...action.payload,
    sysId: action.meta.searchParams.sys_id,
    searchTerm: action.meta.searchParams.sysparm_term,
});

const handleTextSearchFailed = ({ state, updateState, action }) => {
    const { searchErrors } = state;
    const nextSearchErrors = shouldAddError(searchErrors, toSearchError(action))
        ? searchErrors.add(toSearchError(action))
        : searchErrors;
    updateState({
        ...state,
        searchErrors: nextSearchErrors,
    });
};

export default handleTextSearchFailed;
