import transformTypeaheadItemLookupResponse from './transformTypeaheadItemLookupResponse';

const handleTypeaheadItemLookupSucceeded = ({ state, updateState, action }) =>
    updateState({
        ...state,
        typeaheadItem: {
            isFetching: false,
            error: null,
            ...transformTypeaheadItemLookupResponse(action.payload),
        },
    });

export default handleTypeaheadItemLookupSucceeded;
