import transformdrilldownResponse from './transformDrilldownResponse';

const handleDrilldownFetchSucceededEffect = ({ state, updateState, action }) =>
    updateState({
        ...state,
        drilldownData: {
            isFetching: false,
            error: null,
            ...transformdrilldownResponse(action.payload),
        },
    });

export default handleDrilldownFetchSucceededEffect;
