import get from 'lodash/get';
import isNumber from 'lodash/isNumber';
import isFinite from 'lodash/isFinite';
import isEmpty from 'lodash/isEmpty';

import { createLog } from '../../utils/Logger';

const LOG = createLog('snSearchSourcesPresource');
const PRESOURCE_DATA_CONFIG_PATH =
    'data.GlideRecord_Query.sys_aw_master_config._results[0].global_search._reference.associatedConfigs.GlideRecord_Query.sys_aw_global_search_config._results[0]';
const GQL_FETCH_DATA_CONFIG_PATH =
    'data.GlideRecord_Query.sys_aw_global_search_config._results[0]';

const HIDE_SEARCH_RESULTS_PATH =
    'data.GlideRecord_Query.sys_aw_master_config._results[0]';

const parsePropertiesFromPath = (response, { path, properties }) => {
    const result = {};
    const config = get(response, path);
    if (config) {
        properties.forEach(({ name, type }) => {
            const value = get(config, [name, 'value']);

            switch (type) {
                case 'number':
                    if (
                        (isNumber(value) && isFinite(value)) ||
                        !isEmpty(value)
                    ) {
                        result[name] = Number(value);
                    }
                    break;
                case 'boolean':
                    result[name] = Boolean(value);
                    break;
                default:
                    result[name] = value;
            }
        });
    } else {
        LOG.error(
            `Unable to process Global Search Config presource with path: ${path}`
        );
    }
    return result;
};

export default (
    response,
    usePresourcePath = true,
    includeHideSearchResultsCount = true
) => {
    let config = {};
    const paths = [
        {
            path: usePresourcePath
                ? PRESOURCE_DATA_CONFIG_PATH
                : GQL_FETCH_DATA_CONFIG_PATH,
            properties: [
                { name: 'recent_items_count', type: 'number' },
                { name: 'recent_terms_count', type: 'number' },
                { name: 'recent_ttl_in_days', type: 'number' },
                { name: 'tab_overview_items_per_section', type: 'number' },
                { name: 'tab_drilldown_items_per_page', type: 'number' },
            ],
        },
    ];

    if (includeHideSearchResultsCount)
        paths.push({
            path: usePresourcePath ? HIDE_SEARCH_RESULTS_PATH : 'props',
            properties: [
                { name: 'hide_search_results_count', type: 'boolean' },
            ],
        });

    paths.forEach(path => {
        config = { ...config, ...parsePropertiesFromPath(response, path) };
    });

    return config;
};
