import PropTypes from 'prop-types';

export const exactMatchDataShape = PropTypes.shape({
    isFetching: PropTypes.bool,
    error: PropTypes.object,
    term: PropTypes.string,
    sources: PropTypes.shape({
        EXACT_MATCH_SECTION: PropTypes.shape({
            name: PropTypes.string,
            condition: PropTypes.string,
            databaseRecordCount: PropTypes.number,
            records: PropTypes.arrayOf(PropTypes.object),
            displayFields: PropTypes.arrayOf(PropTypes.object),
        }),
    }),
});

export const drilldownDataShape = PropTypes.shape({
    isFetching: PropTypes.bool,
    error: PropTypes.object,
    term: PropTypes.string,
    sourceData: PropTypes.shape({
        name: PropTypes.string,
        condition: PropTypes.shape({
            display: PropTypes.string,
            value: PropTypes.string,
        }),
        databaseRecordCount: PropTypes.number,
        tableName: PropTypes.string,
        records: PropTypes.any,
        displayFields: PropTypes.any,
    }),
    sourceSysId: PropTypes.string,
    responseRecordCount: PropTypes.number,
});
