import React from 'react';
import PropTypes from 'prop-types';
import lowerCase from 'lodash/lowerCase';
import upperFirst from 'lodash/upperFirst';
import truncate from 'lodash/truncate';
import { t } from 'sn-translate';
import '@servicenow/now-label-value';
import InvisibleContent from '../../../common/InvisibleContent';
import getAccessibleDisplayValueMessage from '../../../utils/getAccessibleDisplayValueMessage';

function getDisplayedValue(value, emptyValue) {
    return value && value.length ? value : emptyValue;
}

const getItems = (fields, hideEmptyFields) => {
    const emptyValue = !hideEmptyFields && t('None');

    return fields
        .filter(({ value }) => Boolean(getDisplayedValue(value, emptyValue)))
        .map(({ value, label }) => {
            const valueToDisplay = getDisplayedValue(value, emptyValue);
            const titledCaseName = upperFirst(lowerCase(label));
            const truncatedValue = truncate(valueToDisplay, {
                length: 50,
                separator: /[ ,?:]/,
                omission: '...',
            });
            return {
                label: titledCaseName,
                value: {
                    type: 'string',
                    value: truncatedValue,
                },
            };
        });
};

const FieldList = props => {
    const { descriptionId, fields, hideEmptyFields } = props;
    const stackedValueItems = getItems(fields, hideEmptyFields);

    return (
        <div className="sn-list-group--content sn-list-group--content__meta">
            <now-label-value-stacked
                align="horizontal-equal"
                truncated
                items={stackedValueItems}
                size="sm"
            />
            <InvisibleContent id={descriptionId}>
                {getAccessibleDisplayValueMessage('', stackedValueItems)}
            </InvisibleContent>
        </div>
    );
};

FieldList.propTypes = {
    fields: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
    descriptionId: PropTypes.string.isRequired,
    hideEmptyFields: PropTypes.bool,
};

FieldList.defaultProps = {
    hideEmptyFields: false,
};

export default FieldList;
