class GlobalSearchModel {
    constructor() {
        this.totalResults = 0;
        this.sources = [];
        this.resultSectionsRecordLength = [];
        this.mapSourceIdToSourceData = {};
        this.mapTableNameToSourceId = {};
    }

    // total number of sources
    get totalSourcesRecieved() {
        return this.sources.length;
    }

    get sections() {
        return this.sources
            .filter(
                source =>
                    this.mapSourceIdToSourceData[source.sys_id].record_count > 0
            )
            .map(source => source.sys_id);
    }

    get sectionsFilteredByResponseCount() {
        return this.sources
            .filter(
                source =>
                    this.mapSourceIdToSourceData[source.sys_id].records.length >
                    0
            )
            .map(source => source.sys_id);
    }

    get size() {
        return this.totalResults;
    }

    get dataSize() {
        return this.totalResults - 1;
    }

    get filteredSectionRecordLengths() {
        return this.resultSectionsRecordLength.filter(x => x > 0);
    }

    get sectionRecordLengths() {
        return this.resultSectionsRecordLength;
    }

    getSectionIdByTableName(tableName) {
        return this.mapTableNameToSourceId[tableName];
    }

    getSourceData(sourceId) {
        const data = this.mapSourceIdToSourceData[sourceId];
        if (!data) return {};
        return data;
    }

    getSectionRecordsBySectionId(sourceId) {
        const { records } = this.getSourceData(sourceId);
        if (!records) return [];
        return records;
    }

    getSectionRecordCountBySectionId(sourceId) {
        return this.getSourceData(sourceId).record_count;
    }

    getSectionNameBySectionId(sourceId) {
        return this.getSourceData(sourceId).name;
    }

    getRecordsLengthBySectionId(sourceId) {
        return this.getSectionRecordsBySectionId(sourceId).length;
    }

    getDisplayFieldNamesBySourceId(sourceId) {
        return this.getSourceData(sourceId).fields;
    }
}

export default GlobalSearchModel;
