import { createCustomElement } from '@servicenow/ui-core';
import react from '@servicenow/ui-renderer-react';
import rtlBehavior from '@servicenow/behavior-rtl';
import {
    GLOBAL_SEARCH_CONFIG,
    USER_PREFERENCE,
    presourceFactory,
} from '../behaviors';
import ComponentWorkspaceGlobalSearchTypeahead from './ComponentWorkspaceGlobalSearchTypeahead';
import style from './_componentWorkspaceGlobalSearchTypeahead.scss';
import RecentActionsModel from '../utils/RecentActionsModel';
import actionHandlers from './actionHandlers';

const customElementTagName = 'sn-component-workspace-global-search-typeahead';

/**
 * Search typeahead allows user to search and provides search autocomplete capability while user is typing.
 * It also provides the functionality to perform exact match for records.
 *
 * ```jsx
 * <sn-component-workspace-global-search-typeahead
 *      globalSearchViewConfigId="13453dsf12324356"
 * />
 * ```
 *
 * @seismicElement sn-component-workspace-global-search-typeahead
 * @summary Search typeahead component that allows asearch, autocomplete and exact match functionalities.
 */
export default createCustomElement(customElementTagName, {
    view: ComponentWorkspaceGlobalSearchTypeahead,
    renderer: { type: react },
    behaviors: [
        presourceFactory.get(GLOBAL_SEARCH_CONFIG),
        presourceFactory.get(USER_PREFERENCE),
        { behavior: rtlBehavior },
    ],
    initialState: {
        userPreferences: {
            typeaheadRecentActionsModel: new RecentActionsModel(),
        },
        workspaceConfigs: null,
    },
    properties: {
        /**
         * Reference to a record in sys_aw_global_search_config which configures the component view.
         * @type {string}
         */
        globalSearchViewConfigId: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Computed property for component to know where it's being rendered - UIB or legacy workspaces.
         */
        isLegacyWorkspace: {
            computed({ properties: { globalSearchViewConfigId } }) {
                return !globalSearchViewConfigId;
            },
        },
    },
    actionHandlers,
    styles: [style],
    dispatches: {
        /**
         * Dispatched when a search is executed. Payload contains search term and global search view config id,
         * which is a reference to a record in sys_aw_global_search_config.
         * @type {{searchTerm: string, globalSearchViewConfigId: string}}
         */
        'SN_COMPONENT_WORKSPACE_GLOBAL_SEARCH_TYPEAHEAD#SEARCH_EXECUTED': {
            schema: {
                type: 'object',
                properties: {
                    searchTerm: { type: 'string' },
                    globalSearchViewConfigId: { type: 'string' },
                },
            },
        },
        /**
         * Dispatched when a recently searched item is selected from the popover. Payload contains table and sysId of the record selected.
         * @type {{table: string, sysId: string}}
         */
        'SN_COMPONENT_WORKSPACE_GLOBAL_SEARCH_TYPEAHEAD#ITEM_SELECTED': {
            schema: {
                type: 'object',
                properties: {
                    table: { type: 'string' },
                    sysId: { type: 'string' },
                },
            },
        },
    },
});
