import React from 'react';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import trim from 'lodash/trim';
import debounce from 'lodash/debounce';

import TypeaheadPopover from './TypeaheadPopover/TypeaheadPopover';
import { connectConfig } from '../lib/sn-component-workspace-config';
import ExactMatchModel from '../utils/ExactMatchModel';
import { canTermMatchExactly } from '../utils/workspaceExactMatchTypeahead';
import { EXACT_MATCH_FETCH } from './actions';
import { exactMatchDataShape } from './propTypes';

class ComponentExactMatch extends React.PureComponent {
    constructor(props) {
        super(props);

        this.exactMatchResultModel = new ExactMatchModel();
        this.debouncedRead = debounce(
            this.readWorkspaceExactMatch,
            props.readDebounce
        );
    }

    componentWillReceiveProps(nextProps) {
        const { data: currentData, readDebounce } = this.props;
        const { data: nextData, typeaheadInputValue } = nextProps;

        if (readDebounce !== nextProps.readDebounce) {
            this.debouncedRead = debounce(
                this.readWorkspaceExactMatch,
                nextProps.readDebounce
            );
        }
        const nextDataTerm = get(nextData, 'term');
        const currentTerm = trim(get(this.props, 'typeaheadInputValue', ''));
        if (
            nextData &&
            nextData !== currentData &&
            nextDataTerm === currentTerm
        )
            this.exactMatchResultModel = new ExactMatchModel(nextData);
        else {
            this.exactMatchResultModel = new ExactMatchModel();
            this.exactMatchResultModel.term = typeaheadInputValue;
        }
    }

    componentDidUpdate(prevProps) {
        const currentTerm = trim(get(this.props, 'typeaheadInputValue', ''));
        const prevTerm = trim(get(prevProps, 'typeaheadInputValue', ''));

        if (currentTerm !== prevTerm) this.debouncedRead(currentTerm);
    }

    readWorkspaceExactMatch(term) {
        const {
            helpers: { dispatch },
            isExactMatchEnabled,
            requestCriterionRegex,
        } = this.props;

        if (
            !canTermMatchExactly(
                term,
                requestCriterionRegex,
                isExactMatchEnabled
            )
        )
            return;

        dispatch(EXACT_MATCH_FETCH, {
            term,
        });
    }

    render() {
        const {
            hasActiveRecord,
            isVisible,
            navigateInDirection,
            onActiveRecordUpdate,
            onTypeaheadRecordClick,
        } = this.props;

        const className = isVisible ? undefined : 'hidden';
        return (
            <TypeaheadPopover
                id="sncwsgs-typeahead-sections"
                className={className}
                onRecordSelect={onTypeaheadRecordClick}
                dataModel={this.exactMatchResultModel}
                navigateInDirection={navigateInDirection}
                onActiveRecordUpdate={onActiveRecordUpdate}
                showSearchAllSection
                hasActiveRecord={hasActiveRecord}
            />
        );
    }
}

ComponentExactMatch.propTypes = {
    data: exactMatchDataShape,
    // eslint-disable-next-line react/forbid-prop-types
    helpers: PropTypes.func.isRequired,
    typeaheadInputValue: PropTypes.string.isRequired,
    readDebounce: PropTypes.number.isRequired,
    requestCriterionRegex: PropTypes.string.isRequired,

    isVisible: PropTypes.bool.isRequired,
    isExactMatchEnabled: PropTypes.bool.isRequired,

    navigateInDirection: PropTypes.string.isRequired,
    hasActiveRecord: PropTypes.bool.isRequired,
    onActiveRecordUpdate: PropTypes.func.isRequired,
    onTypeaheadRecordClick: PropTypes.func.isRequired,
};

ComponentExactMatch.defaultProps = {
    data: undefined,
};

function mapConfigToProps(config) {
    return {
        readDebounce: config.global_search_config.typeahead_debounce,
        requestCriterionRegex:
            config.global_search_config.exact_match_request_criterion_regex,
        isExactMatchEnabled: config.global_search_config.exact_match_enabled,
    };
}

export default connectConfig(mapConfigToProps)(ComponentExactMatch);
