import get from 'lodash/get';
import { t } from 'sn-translate';

import { createLog } from './Logger';

const ALL_FAILED_ERROR = 'ALL_FAILED_ERROR';
const NETWORK_ERROR = 'NETWORK_ERROR';
const SEARCH_SOURCE_TABLE_NOT_FOUND_ERROR =
    'SEARCH_SOURCE_TABLE_NOT_FOUND_ERROR'; // log use only
const OTHER_ERROR = 'OTHER_ERROR';

const ERROR_FOR_USER = {
    ALL_FAILED_ERROR: t(
        'An unexpected error lost all the search results. Please search again to find them.'
    ),
    NETWORK_ERROR: t(
        'An unexpected error lost some of the search results. Please search again to find them.'
    ),
    OTHER_ERROR: t(
        'An unexpected error occurred. Please contact your system administrator.'
    ),
};

function isSearchSourceTableNotFound(error) {
    return /Search source table.*does not exist/.test(
        get(error, 'data.error.detail')
    );
}

class SearchSourceErrors {
    constructor(errors = []) {
        this.errors = errors;
    }

    add(error) {
        if (error.message === 'Network Error') {
            return new SearchSourceErrors([
                ...this.errors,
                {
                    type: NETWORK_ERROR,
                    error,
                },
            ]);
        }

        if (isSearchSourceTableNotFound(error)) {
            return new SearchSourceErrors([
                ...this.errors,
                {
                    type: SEARCH_SOURCE_TABLE_NOT_FOUND_ERROR,
                    error,
                },
            ]);
        }

        return new SearchSourceErrors([
            ...this.errors,
            {
                type: OTHER_ERROR,
                error,
            },
        ]);
    }

    hasError(errorType) {
        return this.errors.filter(error => error.type === errorType).length > 0;
    }

    getAggregatedError(numSearchSources, LOG = createLog('GlobalSearch')) {
        if (this.length === 0) {
            return null;
        }
        if (this.length === numSearchSources) {
            LOG.debug(
                'All search source requests failed, Error object : ',
                this.errors
            );
            return ERROR_FOR_USER[ALL_FAILED_ERROR];
        }
        if (this.hasError(NETWORK_ERROR)) {
            LOG.debug(
                'Some search source requests failed, Error object : ',
                this.errors
            );
            return ERROR_FOR_USER[NETWORK_ERROR];
        }
        if (this.hasError(SEARCH_SOURCE_TABLE_NOT_FOUND_ERROR)) {
            LOG.debug(
                'Some search source tables cannot be found, Error object : ',
                this.errors
            );
        } else {
            LOG.debug(
                'Unexpected errors found found, Error object : ',
                this.errors
            );
        }
        return ERROR_FOR_USER[OTHER_ERROR];
    }

    get length() {
        return this.errors.length;
    }
}

export default SearchSourceErrors;
