import get from 'lodash/get';

const transformTypeaheadExactMatchResponse = response => {
    const term = get(response, 'result.term');
    const record = get(response, 'result.search_results.0.records.0');
    const displayFields = get(response, 'result.search_results.0.fields');

    if (term && !record) {
        return {
            hasExactMatch: false,
            term,
        };
    }

    if (!term || !record || !displayFields) return undefined;

    const { metadata, sys_id: sysId, table } = record;

    return {
        term,
        record,
        metadata,
        displayFields,
        sys_id: sysId,
        table,
        hasExactMatch: true,
    };
};

export default transformTypeaheadExactMatchResponse;
