import PropTypes from 'prop-types';
import React from 'react';
import { connectConfig } from '../lib/sn-component-workspace-config';
import { userPreferenceDataShape } from './propTypes';
import TypeaheadPopover from './TypeaheadPopover/TypeaheadPopover';

class ComponentRecentActions extends React.PureComponent {
    render() {
        const {
            isVisible,
            navigateInDirection,
            onActiveRecordUpdate,
            onTypeaheadRecordClick,
            hasActiveRecord,
            userPreferences: { typeaheadRecentActionsModel },
            className,
        } = this.props;

        const isNotVisible = !typeaheadRecentActionsModel.size || !isVisible;

        // don't use the incoming prop if dropdown is not visible
        const resolvedNavigateInDirection = isNotVisible
            ? ''
            : navigateInDirection;

        return (
            <TypeaheadPopover
                id="sncwsgs-typeahead-sections"
                className={className}
                onRecordSelect={onTypeaheadRecordClick}
                dataModel={typeaheadRecentActionsModel}
                navigateInDirection={resolvedNavigateInDirection}
                hasActiveRecord={hasActiveRecord}
                onActiveRecordUpdate={onActiveRecordUpdate}
            />
        );
    }
}

/* eslint-disable react/forbid-prop-types */
ComponentRecentActions.propTypes = {
    userPreferences: userPreferenceDataShape,
    isVisible: PropTypes.bool.isRequired,
    navigateInDirection: PropTypes.string.isRequired,
    hasActiveRecord: PropTypes.bool.isRequired,
    onActiveRecordUpdate: PropTypes.func.isRequired,
    onTypeaheadRecordClick: PropTypes.func.isRequired,
    className: PropTypes.string,
};

ComponentRecentActions.defaultProps = {
    userPreferences: undefined,
    className: '',
};

function mapConfigToProps(config) {
    return {
        maxItemsToShow: config.global_search_config.recent_items_count,
        maxTermsToShow: config.global_search_config.recent_terms_count,
        actionsTTLInDays: config.global_search_config.recent_ttl_in_days,
    };
}

export default connectConfig(mapConfigToProps)(ComponentRecentActions);
