import React from 'react';
import PropTypes from 'prop-types';

const InvisibleContent = props => {
    const { children } = props;
    const { className: providedClassname, ...propsWithoutClass } = props;

    const mergedClassName = `${
        providedClassname ? `${providedClassname} ` : ''
    }snu-sr`;

    return (
        <div className={mergedClassName} {...propsWithoutClass}>
            {children}
        </div>
    );
};

InvisibleContent.propTypes = {
    children: PropTypes.oneOfType([
        PropTypes.arrayOf(PropTypes.node),
        PropTypes.node,
    ]).isRequired,
    className: PropTypes.string,
};

InvisibleContent.defaultProps = {
    className: undefined,
};

export default InvisibleContent;
