import React from 'react';
import PropTypes from 'prop-types';
import scrollIntoViewIfNeeded from '../lib/scroll-into-view-if-needed';

import ResultRecord from './ResultRecord';
import TermRecord from './TermRecord';

import '../componentWorkspaceGlobalSearchTypeaheadItemRecord/componentWorkspaceGlobalSearchTypeaheadItemRecordElement';

export const ENUM_TYPEAHEAD_RECORD_TYPES = {
    TERM: 1,
    RESULT: 2,
    SEARCH_ALL_ACTION: 3,
};

const populateDisplayFields = displayFields => {
    if (displayFields) {
        return displayFields.map(field => field.name);
    }
    return [];
};

class TypeaheadRecord extends React.Component {
    static get TYPES() {
        return ENUM_TYPEAHEAD_RECORD_TYPES;
    }

    static populateDisplayFields(displayFields) {
        if (displayFields) {
            return displayFields.map(field => field.name);
        }
        return [];
    }

    componentDidMount() {
        const { active } = this.props;
        if (active) {
            this.scrollIntoView();
        }
    }

    componentDidUpdate() {
        const { active } = this.props;
        if (active) {
            this.scrollIntoView();
        }
    }

    scrollIntoView() {
        if (this.elementRef) {
            const centerIfNeeded = false;
            scrollIntoViewIfNeeded(this.elementRef, centerIfNeeded);
        }
    }

    render() {
        const {
            record,
            displayFields,
            active,
            type,
            onRecordSelect,
            onMouseMove,
            sectionIndex,
            recordIndex,
        } = this.props;

        const className = `sn-list-group--item ${active ? 'is-hover' : ''}`;
        return (
            <li
                role="option"
                aria-selected={active}
                id={`sncwsgs-typeahead-record-${sectionIndex}-${recordIndex}`}
                ref={ref => {
                    this.elementRef = ref;
                }}
                className={className}
                onMouseDown={event => {
                    event.preventDefault();
                    onRecordSelect();
                }}
                onMouseMove={onMouseMove}
            >
                {(type === this.constructor.TYPES.TERM && (
                    <TermRecord record={record} />
                )) ||
                    (type === this.constructor.TYPES.RESULT &&
                        displayFields &&
                        displayFields.length && (
                            <ResultRecord
                                record={record}
                                displayFields={populateDisplayFields(
                                    displayFields
                                )}
                            />
                        )) || (
                        <sn-component-workspace-global-search-typeahead-item-record
                            table={record.table}
                            sys-id={record.sys_id}
                        />
                    )}
            </li>
        );
    }
}

TypeaheadRecord.propTypes = {
    record: PropTypes.oneOfType([PropTypes.string, PropTypes.object])
        .isRequired,
    active: PropTypes.bool.isRequired,
    onRecordSelect: PropTypes.func.isRequired,
    onMouseMove: PropTypes.func.isRequired,
    type: PropTypes.oneOf(Object.values(TypeaheadRecord.TYPES)).isRequired,
    // eslint-disable-next-line react/forbid-prop-types
    displayFields: PropTypes.array,
    sectionIndex: PropTypes.number.isRequired,
    recordIndex: PropTypes.number.isRequired,
};

TypeaheadRecord.defaultProps = {
    displayFields: undefined,
};

export default TypeaheadRecord;
