import transforms from './transformMaps';
import { createPresourceBehaviorMap } from './util';

/**
 * Map of presource behaviors to be used by Seismic components
 */
const behaviorMap = createPresourceBehaviorMap(transforms);

export default {
    /**
     * Gets a specified Prefetched Resource Behavior
     *
     * @param {string} behavior - Name of the behavior to be return by the factory
     * @return {string} Behavior resources that can be included in Seismic elements
     */
    get: behavior => behaviorMap[behavior],
};
