import React from 'react';
import PropTypes from 'prop-types';
import take from 'lodash/take';
import { t } from 'sn-translate';
import Thumbnail from './Thumbnail';

const MAX_DISPLAY_FIELDS_TO_SHOW = 5;

const UserCard = props => {
    const { onSelect, title, displayFields, thumbnail, initials } = props;

    const selectedDisplayFields = take(
        displayFields,
        MAX_DISPLAY_FIELDS_TO_SHOW
    );

    return (
        <li className="sn-list-group--item sn-list-group--item__grid">
            <a
                onClick={onSelect}
                onKeyDown={onSelect}
                href="javascript:void(0)" // eslint-disable-line no-script-url
                data-testclass="sn-global-search-record"
            >
                <div className="sn-global-search--content sn-global-search--content__static">
                    <Thumbnail
                        type="circle"
                        url={thumbnail}
                        name={title}
                        initials={initials}
                    />
                </div>
                <div className="sn-global-search--content">
                    <span className="sn-list-group--item--title">
                        <now-heading
                            className="global-search-card--title"
                            label={title}
                            level="3"
                            has-no-margin
                            variant="title-primary"
                            wont-wrap
                        />
                    </span>
                    {selectedDisplayFields.length > 0 && (
                        <div className="sn-list-group--content sn-list-group--content__meta">
                            {selectedDisplayFields.map(field => (
                                <span key={field.name}>
                                    {field.value || t('None')}
                                </span>
                            ))}
                        </div>
                    )}
                </div>
            </a>
        </li>
    );
};

UserCard.propTypes = {
    onSelect: PropTypes.func.isRequired,
    title: PropTypes.string.isRequired,
    thumbnail: PropTypes.string,
    initials: PropTypes.string,
    displayFields: PropTypes.array, // eslint-disable-line react/forbid-prop-types
};

UserCard.defaultProps = {
    thumbnail: undefined,
    initials: undefined,
    displayFields: undefined,
};

export default UserCard;
