import React from 'react';
import PropTypes from 'prop-types';
import map from 'lodash/map';
import { t } from 'sn-translate';

import ResultSection from './ResultSection';
import NavigationWrapper from '../utils/NavigationWrapper';

import SnPopover from './SnPopover/SnPopover';
import SnPopoverBody from './SnPopover/SnPopoverBody';
import SnPopoverFooter from './SnPopover/SnPopoverFooter';
import TypeaheadRecord from './TypeaheadRecord';

class TypeaheadPopover extends React.PureComponent {
    static createNewActiveRecord(type, record, sourceId = '') {
        if (type === TypeaheadRecord.TYPES.TERM) {
            return {
                type,
                term: record,
            };
        }
        if (type === TypeaheadRecord.TYPES.RESULT) {
            return {
                type,
                item: record,
                sourceId,
            };
        }
        if (type === TypeaheadRecord.TYPES.SEARCH_ALL_ACTION) {
            return {
                type,
            };
        }
        return undefined;
    }

    constructor(props) {
        super(props);

        this.state = {
            activeRecordFlattenedIndex: -1,
        };
    }

    componentDidMount() {
        const { navigateInDirection, dataModel } = this.props;
        this.consumeNavigationProp(navigateInDirection, dataModel);
    }

    componentWillReceiveProps(nextProps) {
        const { navigateInDirection, dataModel, hasActiveRecord } = nextProps;

        if (!hasActiveRecord) {
            this.setState({ activeRecordFlattenedIndex: -1 });
        }

        this.consumeNavigationProp(navigateInDirection, dataModel);
    }

    consumeNavigationProp(direction, dataModel) {
        const { dataModel: dataModelFromProps } = this.props;
        const { activeRecordFlattenedIndex: indexFromState } = this.state;

        if (dataModelFromProps.term !== dataModel.term) {
            this.updateActiveRecord(-1);
        }
        const activeRecordFlattenedIndex = NavigationWrapper.getUpdatedIndex(
            dataModel,
            indexFromState,
            direction
        );
        this.updateActiveRecord(activeRecordFlattenedIndex, dataModel);
    }

    // eslint-disable-next-line react/destructuring-assignment
    updateActiveRecord(flattenedIndex, dataModel = this.props.dataModel) {
        const { onActiveRecordUpdate } = this.props;
        if (typeof flattenedIndex !== 'number') {
            return;
        }
        let activeRecord = false;
        const { record, type } = NavigationWrapper.getRecord(
            dataModel,
            flattenedIndex
        );
        const indices = NavigationWrapper.getSectionAndRecordIndices(
            dataModel,
            flattenedIndex
        );
        if (type === TypeaheadRecord.TYPES.SEARCH_ALL_ACTION) {
            activeRecord = TypeaheadPopover.createNewActiveRecord(type);
        } else if (type === TypeaheadRecord.TYPES.TERM) {
            activeRecord = TypeaheadPopover.createNewActiveRecord(type, record);
        } else if (type === TypeaheadRecord.TYPES.RESULT) {
            const sourceId =
                dataModel.getSectionIdByTableName(record.table || '') ||
                record.__sourceId ||
                '';
            activeRecord = TypeaheadPopover.createNewActiveRecord(
                type,
                record,
                sourceId
            );
        }
        onActiveRecordUpdate(activeRecord, indices);
        this.setState({ activeRecordFlattenedIndex: flattenedIndex });
    }

    renderFooter(sectionsLength) {
        const { showSearchAllSection } = this.props;
        const { activeRecordFlattenedIndex } = this.state;

        if (!showSearchAllSection) return null;

        const { dataModel, onRecordSelect } = this.props;
        const isActive = activeRecordFlattenedIndex === dataModel.size - 1;
        const footerClassName = isActive ? 'is-hover' : '';

        return (
            <SnPopoverFooter
                onMouseDown={onRecordSelect(dataModel.term)}
                onMouseMove={() => this.updateActiveRecord(dataModel.size - 1)}
            >
                <div className="sn-url">
                    <span
                        role="option"
                        aria-selected={isActive}
                        aria-label={t('Search All Records')}
                        className={footerClassName}
                        id={`sncwsgs-typeahead-record-${sectionsLength}-0`}
                    >
                        {t('Search All Records')}
                    </span>
                </div>
            </SnPopoverFooter>
        );
    }

    render() {
        const { dataModel, onRecordSelect, className, id } = this.props;
        const { activeRecordFlattenedIndex: indexFromState } = this.state;

        const {
            sectionIndex: activeSectionIndex,
            recordIndex: activeRecordIndex,
        } = NavigationWrapper.getSectionAndRecordIndices(
            dataModel,
            indexFromState
        );

        const onSectionMouseMove = sectionIndex => recordIndex => {
            const activeRecordFlattenedIndex = NavigationWrapper.getFlattenedIndex(
                dataModel,
                sectionIndex,
                recordIndex
            );
            this.updateActiveRecord(activeRecordFlattenedIndex, dataModel);
        };

        const sectionsToRender = dataModel.sectionsFilteredByResponseCount;

        return (
            <SnPopover className={className} id={id}>
                <SnPopoverBody>
                    <ul className="sn-list-group result-section" role="listbox">
                        {map(sectionsToRender, (sectionId, index) => (
                            <ResultSection
                                key={sectionId}
                                name={dataModel.getSectionNameBySectionId(
                                    sectionId
                                )}
                                displayFields={dataModel.getDisplayFieldNamesBySourceId(
                                    sectionId
                                )}
                                activeRecordIndex={
                                    activeSectionIndex === index
                                        ? activeRecordIndex
                                        : -1
                                }
                                results={dataModel.getSectionRecordsBySectionId(
                                    sectionId
                                )}
                                onMouseMove={onSectionMouseMove(index)}
                                onRecordSelect={onRecordSelect}
                                sourceId={sectionId}
                                sectionIndex={index}
                            />
                        ))}
                    </ul>
                </SnPopoverBody>
                {this.renderFooter(sectionsToRender.length)}
            </SnPopover>
        );
    }
}

TypeaheadPopover.propTypes = {
    onRecordSelect: PropTypes.func.isRequired,
    navigateInDirection: PropTypes.oneOf([
        '',
        ...Object.values(NavigationWrapper.DIRECTIONS),
    ]),
    hasActiveRecord: PropTypes.bool,
    id: PropTypes.string,
    onActiveRecordUpdate: PropTypes.func.isRequired,
    showSearchAllSection: PropTypes.bool,
    // eslint-disable-next-line react/forbid-prop-types
    dataModel: PropTypes.object.isRequired,
    className: PropTypes.string,
};

TypeaheadPopover.defaultProps = {
    hasActiveRecord: null,
    navigateInDirection: '',
    showSearchAllSection: null,
    className: '',
    id: null,
};

export default TypeaheadPopover;
