import { FETCH_CONFIGURATIONS } from '../../actions';

const handlePropertyChangedEffect = ({
    state,
    dispatch,
    action,
    updateState,
}) => {
    const {
        payload: { name },
    } = action;

    const {
        properties: { globalSearchViewConfigId },
    } = state;

    if (name === 'globalSearchViewConfigId') {
        if (globalSearchViewConfigId) {
            dispatch(FETCH_CONFIGURATIONS, {
                globalSearchViewConfigId,
            });

            updateState({
                ...state,
                workspaceConfigs: null,
            });
        }
    }
};

export default handlePropertyChangedEffect;
