const handleDrilldownFetchStartedEffect = ({ state, updateState, action }) => {
    const { sysParmTerm: term, sysId } = action.meta;
    const updatedState = {
        ...state,
        drilldownData: {
            isFetching: true,
            error: null,
        },
    };

    if (!state.drilldownData) {
        updateState(updatedState);
        return;
    }

    const {
        sourceSysId: drilldownSysId,
        term: drilldownTerm,
    } = state.drilldownData;

    // Don't clear out drilldown data if the section is the same
    if (drilldownSysId === sysId && drilldownTerm === term) {
        updatedState.drilldownData = {
            ...state.drilldownData,
            ...updatedState.drilldownData,
        };
    } else {
        // Reset page for every new drilldown
        updatedState.page = 1;
    }
    updateState(updatedState);
};

export default handleDrilldownFetchStartedEffect;
