import transformTypeaheadExactMatchResponse from './transformTypeaheadExactMatchResponse';

const handleTypeaheadExactMatchSucceededEffect = ({
    state,
    updateState,
    action,
}) =>
    updateState({
        ...state,
        exactMatch: {
            isFetching: false,
            error: null,
            ...transformTypeaheadExactMatchResponse(action.payload),
        },
    });

export default handleTypeaheadExactMatchSucceededEffect;
