import has from 'lodash/has';
import { t } from 'sn-translate';
import {
    ITEM_SELECTED,
    RECENT_ACTIONS_MODEL_SET,
    TERM_SEARCHED,
    USER_PREFERENCES_SET,
    USER_PREFERENCES_FETCH,
    SN_WS_SEARCH_TYPEAHEAD_ITEM_SELECTED,
    SN_WS_SEARCH_TYPEAHEAD_SEARCH_EXECUTED,
} from '../../actions';
import { parseUserPreferenceValue } from '../utils';
import RecentActionsModel from '../../../utils/RecentActionsModel';
import getBehaviorProp from '../../../behaviors/util/getBehaviorProp';
import { GLOBAL_SEARCH_CONFIG } from '../../../behaviors/constants';

const addRecentAction = (dispatch, model, type, actionData, metadata) => {
    const stringifiedRecentActions = model.addRecentAction(
        type,
        actionData,
        metadata
    );

    dispatch(USER_PREFERENCES_SET, {
        name: 'workspace.globalSearch.recentActions',
        value: stringifiedRecentActions,
    });
};

const getGlobalConfigs = state => {
    if (state.workspaceConfigs) {
        return state.workspaceConfigs.viewConfig;
    }

    return getBehaviorProp(state, GLOBAL_SEARCH_CONFIG);
};

export default ({ state, action, dispatch }) => {
    const {
        action: payloadAction,
        actionMetadata: payloadActionMetadata,
    } = action.payload;

    const {
        userPreferences,
        properties: { globalSearchViewConfigId },
    } = state;

    const {
        recent_items_count: recentItemsCount,
        recent_terms_count: recentTermsCount,
        recent_ttl_in_days: recentTTLInDays,
    } = getGlobalConfigs(state);

    const {
        lastTermAddedAsRecentAction,
        lastSysIdAddedAsRecentAction,
    } = userPreferences;

    const typeaheadRecentActionsModel = new RecentActionsModel(
        parseUserPreferenceValue(userPreferences),
        recentItemsCount,
        recentTermsCount,
        recentTTLInDays,
        t('Recently Searched'),
        t('Recently Viewed')
    );

    dispatch([RECENT_ACTIONS_MODEL_SET], {
        model: typeaheadRecentActionsModel,
    });

    if (
        payloadAction === ITEM_SELECTED &&
        has(payloadActionMetadata, 'sourceId')
    ) {
        const { item, table, sys_id: sysId } = action.payload;
        const { sourceId } = payloadActionMetadata;
        const recordData = { data: item, table, sys_id: sysId };
        if (lastSysIdAddedAsRecentAction !== sysId && sysId) {
            addRecentAction(
                dispatch,
                typeaheadRecentActionsModel,
                RecentActionsModel.SECTIONS.ITEM_SELECTED,
                recordData,
                { sourceId }
            );
        }
        dispatch(SN_WS_SEARCH_TYPEAHEAD_ITEM_SELECTED, {
            table,
            sysId,
        });
    } else if (payloadAction === TERM_SEARCHED) {
        const { term } = action.payload;
        if (lastTermAddedAsRecentAction !== term && term) {
            addRecentAction(
                dispatch,
                typeaheadRecentActionsModel,
                RecentActionsModel.SECTIONS.TERM_SEARCHED,
                term
            );
        }
        dispatch(SN_WS_SEARCH_TYPEAHEAD_SEARCH_EXECUTED, {
            searchTerm: term,
            globalSearchViewConfigId,
        });
    }

    dispatch(USER_PREFERENCES_FETCH, {
        name: 'workspace.globalSearch.recentActions',
    });
};
