import React from 'react';
import PropTypes from 'prop-types';

import has from 'lodash/has';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import truncate from 'lodash/truncate';
import trim from 'lodash/trim';
import { t } from 'sn-translate';
import { KEY_CODES } from '../../utils/GlobalSearchUtils';
import getCardComponent from './RecordViews/cardFactory';

class Record extends React.PureComponent {
    constructor(props) {
        super(props);

        this.onSelect = this.onSelect.bind(this);
    }

    onSelect(event) {
        const { onSelect, record, section } = this.props;
        if (
            event.type === 'click' ||
            (event.type === 'keydown' &&
                !!event.which &&
                event.which === KEY_CODES.RETURN)
        ) {
            onSelect(record, section);
        }
    }

    render() {
        const { record, fields } = this.props;

        const title =
            has(record, 'metadata.title') &&
            !isEmpty(trim(record.metadata.title))
                ? record.metadata.title
                : t('No Title');
        const description = truncate(get(record, 'metadata.description', ''), {
            length: 350,
            separator: /[ ,?:]/,
            omission: '...',
        });
        const thumbnail = get(record, 'metadata.thumbnail_url');
        const initials = get(record, 'metadata.additional_info.initials');

        const propsToPass = {
            title,
            description,
            thumbnail,
            initials,
            sysId: record.sys_id,
        };

        const displayFields = fields
            .filter(({ name }) => name !== 'sys_id')
            .map(field => ({
                name: field.name,
                label: field.label,
                value: trim(get(record, ['data', field.name, 'display'])),
            }));

        const CardComponent = getCardComponent(record.table);

        return (
            <CardComponent
                onSelect={this.onSelect}
                displayFields={displayFields}
                {...propsToPass}
            />
        );
    }
}

Record.propTypes = {
    onSelect: PropTypes.func.isRequired,
    fields: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
    record: PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
    section: PropTypes.string.isRequired,
};

export default Record;
