const CONFIGURATION_QUERY = `query ($globalSearchViewConfigId: String!, $globalSearchDataConfigId: String!) {
    GlideRecord_Query {
      sys_aw_global_search_config(sys_id: $globalSearchViewConfigId) {
        _results {
          name {
            displayValue
            value
          }
          recent_items_count {
            displayValue
            value
          }
          recent_terms_count {
            displayValue
            value
          }
          recent_ttl_in_days {
            displayValue
            value
          }
          tab_overview_items_per_section {
            displayValue
            value
          }
          tab_drilldown_items_per_page {
            displayValue
            value
          }
        }
      }
      sys_search_context_config(sys_id: $globalSearchDataConfigId) {
        _results {
          sys_id {
            value
          }
          associatedSources: _query {
            GlideRecord_Query {
              m2m_search_context_config_search_source(queryConditions: "search_context_config=$$parent.sys_id$$^ORDERBYorder") {
                _results {
                  order {
                    value
                  }
                  source {
                    value
                    displayValue
                  }
                }
              }
            }
          }
        }
      }
    }
    GlideDomain_Query {
      user {
        ts_match: preferences(name: "ts.match") {
          name
          value
        }
      }
    }
}`;

export default CONFIGURATION_QUERY;
