import SearchSourceErrors from '../../../utils/SearchSourceErrors';
import { createLog } from '../../../utils/Logger';

const LOG = createLog('GlobalSearchTab Configurations');

const handleFetchConfigurationsFailed = ({ state, action, updateState }) => {
    const { searchErrors } = state;
    const errors = searchErrors || new SearchSourceErrors();
    const nextSearchErrors = errors.add(action.payload);

    LOG.error('Component could not retrieve configuration.');

    updateState({
        ...state,
        searchErrors: nextSearchErrors,
    });
};

export default handleFetchConfigurationsFailed;
