import React from 'react';
import PropTypes from 'prop-types';

class TermRecord extends React.PureComponent {
    render() {
        const { record } = this.props;
        return (
            <div aria-label={record}>
                <span className="sn-list-group--content">
                    <span className="sn-preserve-whitepsace">{record}</span>
                </span>
            </div>
        );
    }
}

TermRecord.propTypes = {
    record: PropTypes.string.isRequired,
};

export default TermRecord;
