// Will be fixed with STRY50029007
// Should remove unnecessary nesting
/* eslint-disable react/prefer-stateless-function */
/* eslint-disable react/forbid-prop-types */

import React from 'react';
import PropTypes from 'prop-types';
import merge from 'lodash/merge';

function connectConfig(mapConfigToProps) {
    function wrapComponent(ComponentToWrap) {
        class ComponentWithConfig extends React.Component {
            render() {
                // render props merged with config props

                const { config } = this.context;

                // FIXME: there's an assumption that mapConfigToProps is a function, and returns an object
                const configProps = config ? mapConfigToProps(config) : {};
                const propsWithConfig = merge({}, this.props, configProps);

                return <ComponentToWrap {...propsWithConfig} />;
            }
        }

        // lets define whats needed from the `context`
        ComponentWithConfig.contextTypes = {
            config: PropTypes.object.isRequired,
        };

        return ComponentWithConfig;
    }

    return ComponentToWrap => wrapComponent(ComponentToWrap);
}

export default connectConfig;
