import React from 'react';
import PropTypes from 'prop-types';
import { t } from 'sn-translate';

const SnPopoverFooter = ({ children, className, ...props }) => (
    <div
        className={`sn-popover--footer sn-popover--footer__sm ${className}`}
        role="listbox"
        aria-label={t('Extra Option')}
        {...props}
    >
        {children}
    </div>
);

SnPopoverFooter.propTypes = {
    className: PropTypes.string,
    children: PropTypes.node.isRequired,
};

SnPopoverFooter.defaultProps = {
    className: '',
};

export default SnPopoverFooter;
