const CONFIGURATION_QUERY = `query ($globalSearchViewConfigId: String!) {
    GlideRecord_Query {
      sys_aw_global_search_config(sys_id: $globalSearchViewConfigId) {
        _results {
          name {
            displayValue
            value
          }
          recent_items_count {
            displayValue
            value
          }
          recent_terms_count {
            displayValue
            value
          }
          recent_ttl_in_days {
            displayValue
            value
          }
        }
      }
    }
    GlideDomain_Query {
      user {
        ts_match: preferences(name: "ts.match") {
          name
          value
        }
      }
    }
}`;

export default CONFIGURATION_QUERY;
