import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
    USER_PREFERENCES_SET_STARTED,
    USER_PREFERENCES_SET_SUCCEEDED,
    USER_PREFERENCES_SET_FAILED,
} from '../../actions';

const setUserPreferenceMutation = `
    mutation($name: String!, $value: String!) {
        GlideDomain_Mutation {
            user {
                setPreference(name: $name, value: $value) {
                    name
                    value
                }
            }
        }
    }
`;

const handleTypeaheadUserPreferenceSet = createGraphQLEffect(
    setUserPreferenceMutation,
    {
        variableList: ['name', 'value'],
        startActionType: USER_PREFERENCES_SET_STARTED,
        successActionType: USER_PREFERENCES_SET_SUCCEEDED,
        errorActionType: USER_PREFERENCES_SET_FAILED,
    }
);

export default handleTypeaheadUserPreferenceSet;
