import React from 'react';
import PropTypes from 'prop-types';
import map from 'lodash/map';
import get from 'lodash/get';
import { t } from 'sn-translate';

import { connectConfig } from '../../lib/sn-component-workspace-config';
import Section from './Section';
import Sidebar from './Sidebar';
import TabHeader from './Tab/TabHeader';
import TabBody from './Tab/TabBody';
import TabContainer from './Tab/TabContainer';
import { exactMatchDataShape } from '../propTypes';
import AriaLiveRegion from '../../common/AriaLiveRegion';

export class Overview extends React.PureComponent {
    constructor(props) {
        super(props);

        this.containerNode = null;
        this.getContainerRef = this.getContainerRef.bind(this);
    }

    componentDidUpdate(prevProps) {
        const { term } = this.props;
        if (prevProps.term !== term && this.containerNode) {
            this.containerNode.scrollTop = 0;
        }
    }

    getContainerRef(el) {
        this.containerNode = el;
    }

    renderSection(rawData, sysId, index, hideResultsCount) {
        const {
            scrollIntoViewSection,
            onRecordSelect,
            onSectionDrillDown,
        } = this.props;
        const data = get(rawData, ['sources', sysId]);
        if (!data) return null;
        if (data.databaseRecordCount < 1) return null;

        const shouldScrollIntoView = scrollIntoViewSection === sysId;

        return (
            <Section
                key={sysId}
                focusLinkOnMount={index === 0}
                scrollSectionIntoView={shouldScrollIntoView}
                section={sysId}
                data={data}
                onRecordSelect={onRecordSelect}
                onViewAllClick={onSectionDrillDown}
                hideResultsCount={hideResultsCount}
            />
        );
    }

    render() {
        const {
            term,
            data,
            reading,
            isOverviewVisible,
            exactMatchData,
            exactMatchEnabled,
            searchSources,
            onSectionDrillDown,
            onSidebarItemClick,
            hideResultsCount,
        } = this.props;

        const hasExactMatch = exactMatchData && !!exactMatchData.sources;

        const responseRecordCount = get(data, 'responseRecordCount', 0);
        const totalRecordCount =
            responseRecordCount + (exactMatchEnabled && hasExactMatch ? 1 : 0);

        let headerTitle = '';
        if (reading) {
            headerTitle = t('Searching for "{0}"', term);
        } else if (hideResultsCount) {
            headerTitle = t('Results for "{0}"', term);
        } else {
            headerTitle = t(
                '{0} results for "{1}"',
                totalRecordCount.toLocaleString(),
                term
            );
        }
        const indexOffset = hasExactMatch ? 1 : 0;

        return (
            <TabContainer
                isVisible={isOverviewVisible}
                className="tab-results-container"
            >
                <AriaLiveRegion message={headerTitle} />
                <div
                    className="global-search-container"
                    ref={this.getContainerRef}
                >
                    <TabHeader title={headerTitle} spinnerEnabled={reading} />
                    <TabBody>
                        {exactMatchEnabled &&
                            this.renderSection(
                                exactMatchData,
                                'EXACT_MATCH_SECTION',
                                0,
                                hideResultsCount
                            )}
                        {map(searchSources, ({ sys_id: sysId }, index) =>
                            this.renderSection(
                                data,
                                sysId,
                                index + indexOffset,
                                hideResultsCount
                            )
                        )}
                    </TabBody>
                </div>
                <Sidebar
                    data={data}
                    onSectionDrillDown={onSectionDrillDown}
                    onSidebarItemClick={onSidebarItemClick}
                    displayExactMatchSection={
                        exactMatchEnabled && hasExactMatch
                    }
                    hideResultsCount={hideResultsCount}
                />
            </TabContainer>
        );
    }
}

Overview.propTypes = {
    term: PropTypes.string.isRequired,
    scrollIntoViewSection: PropTypes.string.isRequired,
    reading: PropTypes.bool.isRequired,
    isOverviewVisible: PropTypes.bool.isRequired,
    data: PropTypes.object, // eslint-disable-line react/forbid-prop-types
    searchSources: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
    onRecordSelect: PropTypes.func.isRequired,
    onSectionDrillDown: PropTypes.func.isRequired,
    onSidebarItemClick: PropTypes.func.isRequired,
    exactMatchData: exactMatchDataShape,
    exactMatchEnabled: PropTypes.bool.isRequired,
    hideResultsCount: PropTypes.bool,
};

Overview.defaultProps = {
    data: undefined,
    exactMatchData: undefined,
    hideResultsCount: false,
};

function mapConfigToProps(config) {
    return {
        searchSources: config.search_sources,
        exactMatchEnabled: config.global_search_config.exact_match_enabled,
    };
}

export default connectConfig(mapConfigToProps)(Overview);
