import { FETCH_CONFIGURATIONS } from '../../actions';

const handleBootstrappedEffect = ({ state, dispatch }) => {
    const {
        properties: { globalSearchViewConfigId },
    } = state;
    if (globalSearchViewConfigId) {
        dispatch(FETCH_CONFIGURATIONS, {
            globalSearchViewConfigId,
        });
    }
};

export default handleBootstrappedEffect;
