import React from 'react';
import PropTypes from 'prop-types';

import '@servicenow/now-icon';

const THUMBNAIL_CLASS = {
    circle: 'sn-avatar sn-avatar__xxl',
    square: 'sn-avatar sn-avatar__xxl sn-avatar__square',
};

class Thumbnail extends React.PureComponent {
    constructor(props) {
        super(props);
        this.getAvatarContent = this.getAvatarContent.bind(this);
    }

    getAvatarContent() {
        const { type, url, icon, initials } = this.props;
        const className = THUMBNAIL_CLASS[type];

        if (url && url.length)
            return (
                <div
                    className={className}
                    style={{ backgroundImage: `url(/${url})` }}
                />
            );

        if (icon && icon.length)
            return (
                <div className={className}>
                    <now-icon icon={icon} size="lg" />
                </div>
            );

        return (
            <div className={className} aria-hidden="true">
                {initials}
            </div>
        );
    }

    render() {
        return (
            <div className="sn-avatar-container">{this.getAvatarContent()}</div>
        );
    }
}

Thumbnail.propTypes = {
    type: PropTypes.oneOf(['square', 'circle']).isRequired,
    url: PropTypes.string,
    icon: PropTypes.string, // if url is not present, load icon
    initials: PropTypes.string,
};

Thumbnail.defaultProps = {
    url: undefined,
    icon: undefined,
    initials: undefined,
};

export default Thumbnail;
