import get from 'lodash/get';
import sortBy from 'lodash/sortBy';

import { createLog } from '../../utils/Logger';

const LOG = createLog('snSearchSourcesPresource');
const PRESOURCE_DATA_PATH =
    'data.GlideRecord_Query.sys_aw_master_config._results[0].global_search_data._reference.associatedSources.GlideRecord_Query.m2m_search_context_config_search_source._results';
const GQL_FETCH_DATA_PATH =
    'data.GlideRecord_Query.sys_search_context_config._results[0].associatedSources.GlideRecord_Query.m2m_search_context_config_search_source._results';

export default (response, usePresourcePath = true) => {
    const path = usePresourcePath ? PRESOURCE_DATA_PATH : GQL_FETCH_DATA_PATH;
    const searchSourcesData = get(response, path, []);

    if (!searchSourcesData) {
        LOG.error(`Unable to process Search Source presource`);
        return [];
    }

    const orderedSearchSourceData = sortBy(
        searchSourcesData,
        source => source.order.value
    );

    const sources = orderedSearchSourceData.map(
        ({ source: { value, displayValue } }) => ({
            name: displayValue,
            sys_id: value,
        })
    );

    return sources;
};
