import get from 'lodash/get';

const handleTypeaheadUserPreferenceSetSucceeded = ({
    state,
    updateState,
    action,
}) => {
    const { data } = action.payload;
    const userPreferencesMutation = get(data, 'GlideDomain_Mutation.user');

    const userPreferences = userPreferencesMutation
        ? {
              ...state.userPreferences,
              ...userPreferencesMutation.setPreference,
          }
        : { ...state.userPreferences };

    updateState({
        ...state,
        fetching: false,
        userPreferences,
    });
};

export default handleTypeaheadUserPreferenceSetSucceeded;
