import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { map } from 'lodash';
import '@servicenow/now-heading';

import TypeaheadRecord from './TypeaheadRecord';

class ResultSection extends React.PureComponent {
    render() {
        const {
            results,
            displayFields,
            name,
            activeRecordIndex,
            onRecordSelect,
            onMouseMove,
            sourceId,
            sectionIndex,
        } = this.props;

        return (
            <Fragment>
                <li
                    className="sn-list-group--header"
                    id={`sncwsgs-typeahead-section-header-${sectionIndex}`}
                    role="presentation"
                >
                    <span className="sn-list-group--content">
                        <now-heading
                            has-no-margin
                            aria-label={name}
                            label={name}
                            level="2"
                            variant="title-primary"
                        />
                    </span>
                    <span className="sn-list-group--content" />
                </li>
                {map(results, (record, recordIndex) => (
                    <TypeaheadRecord
                        type={
                            typeof record === 'string'
                                ? TypeaheadRecord.TYPES.TERM
                                : TypeaheadRecord.TYPES.RESULT
                        }
                        record={record}
                        displayFields={displayFields}
                        key={recordIndex}
                        recordIndex={recordIndex}
                        sectionIndex={sectionIndex}
                        onRecordSelect={onRecordSelect(record)}
                        onMouseMove={() => onMouseMove(recordIndex)}
                        active={recordIndex === activeRecordIndex}
                        sourceId={record.__sourceId || sourceId}
                    />
                ))}
            </Fragment>
        );
    }
}

ResultSection.propTypes = {
    name: PropTypes.string.isRequired,
    results: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
    onRecordSelect: PropTypes.func.isRequired,
    onMouseMove: PropTypes.func.isRequired,
    activeRecordIndex: PropTypes.number.isRequired,
    sourceId: PropTypes.string.isRequired,
    displayFields: PropTypes.array, // eslint-disable-line react/forbid-prop-types
    sectionIndex: PropTypes.number.isRequired,
};

ResultSection.defaultProps = {
    displayFields: undefined,
};

export default ResultSection;
