import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
    EXACT_MATCH_FAILED,
    EXACT_MATCH_STARTED,
    EXACT_MATCH_SUCCEEDED,
} from '../../actions';

const handleTabExactMatchFetchEffect = createHttpEffect(
    '/api/now/v1/search/exactmatch?sysparm_term=:term',
    {
        method: 'GET',
        headers: {},
        pathParams: ['term'],
        startActionType: EXACT_MATCH_STARTED,
        successActionType: EXACT_MATCH_SUCCEEDED,
        errorActionType: EXACT_MATCH_FAILED,
    }
);

export default handleTabExactMatchFetchEffect;
