import get from 'lodash/get';

const transformdrilldownResponse = response => {
    const rawResult = get(response, 'result');

    if (!rawResult) return undefined;

    const {
        result_count: resultCount = 0,
        search_results: searchResults = [],
        sources = [],
        term = '',
    } = rawResult;

    const rawData = get(sources, [0]);

    if (!rawData) return undefined;

    const { sys_id: sysId, name, condition, source_table: tableName } = rawData;

    const sectionSearchResults = get(searchResults, [0]);

    if (!sectionSearchResults || sectionSearchResults.sys_id !== sysId)
        return undefined;

    const {
        records,
        record_count: databaseRecordCount,
        fields: displayFields,
    } = sectionSearchResults;

    const sourceData = {
        name: name.display,
        condition,
        databaseRecordCount,
        tableName,
        records,
        displayFields,
    };

    const sourceSysId = sysId;

    if (!sourceSysId || !term) return undefined;

    return {
        sourceData,
        sourceSysId,
        responseRecordCount: resultCount,
        term,
    };
};

export default transformdrilldownResponse;
