import React from 'react';
import PropTypes from 'prop-types';
import { t } from 'sn-translate';
import { connectConfig } from '../../lib/sn-component-workspace-config';

import SearchInput from './SearchInput';
import { exactMatchDataShape } from '../propTypes';
import {
    isExactMatchPresent,
    hasExactMatchReadingFinished,
} from '../../utils/workspaceExactMatchTypeahead';

const getAriaSelectedId = ariaSelected => {
    const { sectionIndex, recordIndex } = ariaSelected;
    if (sectionIndex === -1 && recordIndex === -1) return '';

    return `sncwsgs-typeahead-record-${sectionIndex}-${recordIndex}`;
};

const getAriaLiveText = props => {
    const { typeaheadInputValue } = props;

    if (
        !hasExactMatchReadingFinished(
            props,
            typeaheadInputValue,
            props.requestCriterionRegex
        )
    )
        return '';

    if (isExactMatchPresent(props, typeaheadInputValue))
        return t(
            'Exact match available for "{0}". Press enter to go directly to the record. To navigate to search results page, press the down arrow key until you reach the search results page link.',
            typeaheadInputValue
        );

    return t(
        'Exact match not available for "{0}". To open full results in a tab, press enter key.',
        typeaheadInputValue
    );
};

class ComponentTypeaheadInput extends React.Component {
    static getAriaSelectedId(ariaSelected) {
        const { sectionIndex, recordIndex } = ariaSelected;
        if (sectionIndex === -1 && recordIndex === -1) return '';

        return `sncwsgs-typeahead-record-${sectionIndex}-${recordIndex}`;
    }

    constructor(props) {
        super(props);

        this.inputElement = null;

        this.onChange = this.onChange.bind(this);
        this.onFocus = this.onFocus.bind(this);
        this.onBlur = this.onBlur.bind(this);
        this.onSubmit = this.onSubmit.bind(this);

        this.onSearchIconClick = this.onSearchIconClick.bind(this);
        this.onClearIconClick = this.onClearIconClick.bind(this);
        this.onGeneralKeyDown = this.onGeneralKeyDown.bind(this);
    }

    componentWillReceiveProps(nextProps) {
        const { typeaheadInputValue } = this.props;
        if (
            this.inputElement &&
            nextProps.typeaheadInputValue !== typeaheadInputValue &&
            nextProps.typeaheadInputValue !== this.inputElement.value
        ) {
            this.inputElement.value = nextProps.typeaheadInputValue;
        }
    }

    onBlur() {
        const { handleBlur } = this.props;
        handleBlur();
    }

    onFocus() {
        const { handleFocus } = this.props;
        handleFocus();
    }

    onChange(value) {
        const { handleChange } = this.props;
        handleChange(value);
    }

    onSubmit(value) {
        const { onInputSubmit } = this.props;
        onInputSubmit(value.trim());
    }

    onSearchIconClick() {
        const {
            isDisabled,
            searchExpanded,
            handleSearchIconClick,
        } = this.props;
        if (isDisabled) return;

        if (this.inputElement) {
            handleSearchIconClick();
            if (!searchExpanded) {
                this.inputElement.focus();
            } else {
                this.inputElement.blur();
            }
        }
    }

    onClearIconClick() {
        const { isDisabled, handleClearIconClick } = this.props;

        if (isDisabled) return;

        handleClearIconClick();
        if (this.inputElement) {
            this.inputElement.focus();
            this.inputElement.value = '';
        }
    }

    onGeneralKeyDown() {
        const { searchExpanded, handleGeneralKeyDown } = this.props;
        if (searchExpanded) {
            handleGeneralKeyDown();
        }
    }

    render() {
        const {
            ariaSelected,
            onDownArrowKeyDown,
            onUpArrowKeyDown,
            onEscapeKeyDown,
            typeaheadInputValue,
            searchExpanded,
            dropdownVisible,
            isDisabled,
            isLegacyWorkspace,
        } = this.props;

        return (
            <SearchInput
                value={typeaheadInputValue}
                isExpanded={searchExpanded}
                isDropdownVisible={dropdownVisible}
                isDisabled={isDisabled}
                onFocus={this.onFocus}
                onBlur={this.onBlur}
                onChange={this.onChange}
                onSubmit={this.onSubmit}
                onDownArrowKeyDown={onDownArrowKeyDown}
                onUpArrowKeyDown={onUpArrowKeyDown}
                onEscapeKeyDown={onEscapeKeyDown}
                onGeneralKeyDown={this.onGeneralKeyDown}
                onSearchIconClick={this.onSearchIconClick}
                onClearIconClick={this.onClearIconClick}
                bindInputRef={input => {
                    this.inputElement = input;
                }}
                ariaActiveDescendant={getAriaSelectedId(ariaSelected)}
                ariaLiveText={getAriaLiveText(this.props)}
                isLegacyWorkspace={isLegacyWorkspace}
            />
        );
    }
}

ComponentTypeaheadInput.propTypes = {
    typeaheadInputValue: PropTypes.string,
    searchExpanded: PropTypes.bool,
    dropdownVisible: PropTypes.bool,
    isDisabled: PropTypes.bool,
    handleBlur: PropTypes.func.isRequired,
    handleFocus: PropTypes.func.isRequired,
    handleChange: PropTypes.func.isRequired,
    handleSearchIconClick: PropTypes.func.isRequired,
    handleClearIconClick: PropTypes.func.isRequired,
    handleGeneralKeyDown: PropTypes.func.isRequired,
    onUpArrowKeyDown: PropTypes.func.isRequired,
    onDownArrowKeyDown: PropTypes.func.isRequired,
    onInputSubmit: PropTypes.func.isRequired,
    onEscapeKeyDown: PropTypes.func.isRequired,
    ariaSelected: PropTypes.shape({
        sectionIndex: PropTypes.number.isRequired,
        recordIndex: PropTypes.number.isRequired,
    }),
    // eslint-disable-next-line react/no-unused-prop-types
    exactMatch: exactMatchDataShape,
    isLegacyWorkspace: PropTypes.bool,
};

ComponentTypeaheadInput.defaultProps = {
    typeaheadInputValue: '',
    searchExpanded: false,
    dropdownVisible: false,
    isDisabled: false,
    ariaSelected: {
        sectionIndex: -1,
        recordIndex: -1,
    },
    exactMatch: undefined,
    isLegacyWorkspace: undefined,
};

function mapConfigToProps(config) {
    return {
        requestCriterionRegex:
            config.global_search_config.exact_match_request_criterion_regex,
    };
}

export default connectConfig(mapConfigToProps)(ComponentTypeaheadInput);
