import get from 'lodash/get';

const transformTypeaheadItemLookupResponse = response => {
    const record = get(response, 'result.search_results.0.records.0');
    const rawDisplayFields = get(response, 'result.search_results.0.fields');

    if (!record || !rawDisplayFields) return undefined;

    const displayFields = rawDisplayFields.map(field => field.name);

    return {
        record,
        displayFields,
    };
};

export default transformTypeaheadItemLookupResponse;
