/* eslint-disable */
// Copied over from sn-component-form
const LOG_STATUS_KEY = '__SEISMIC_LOG_LEVEL';

export const LOG_LEVEL_ALL = 0;
export const LOG_LEVEL_DEBUG = 1;
export const LOG_LEVEL_LOG = 2;
export const LOG_LEVEL_INFO = 2;
export const LOG_LEVEL_WARN = 3;
export const LOG_LEVEL_ERROR = 4;

// set default logging level
if (typeof window[LOG_STATUS_KEY] === 'undefined') {
    window[LOG_STATUS_KEY] = LOG_LEVEL_ERROR;
}

export function enable(logLevel = LOG_LEVEL_ALL) {
    switch (logLevel) {
        case LOG_LEVEL_ALL:
        case LOG_LEVEL_DEBUG:
        case LOG_LEVEL_LOG:
        case LOG_LEVEL_INFO:
        case LOG_LEVEL_WARN:
        case LOG_LEVEL_ERROR:
            window[LOG_STATUS_KEY] = logLevel;
            break;
        default:
            logLevel = LOG_LEVEL_ERROR;
    }
}

export function disable() {
    window[LOG_STATUS_KEY] = LOG_LEVEL_ERROR;
}

/**
 * Simple logging utilty to include context
 * @usage const LOG = createLog('ComponentName');
 *
 * @param name
 * @return {{log, debug, warn, error}}
 */
export function createLog(name) {
    function _log(type, level, args) {
        if (level < window[LOG_STATUS_KEY]) {
            return;
        }
        args = Array.prototype.splice.call(args, 0);
        args.unshift(`(${name})`);
        // eslint-disable-next-line no-console
        console[type].apply(console, args);
    }

    return {
        debug() {
            _log('debug', LOG_LEVEL_DEBUG, arguments);
        },
        log() {
            _log('log', LOG_LEVEL_LOG, arguments);
        },
        info() {
            _log('info', LOG_LEVEL_INFO, arguments);
        },
        warn() {
            _log('warn', LOG_LEVEL_WARN, arguments);
        },
        error() {
            _log('error', LOG_LEVEL_ERROR, arguments);
        },
    };
}
