import get from 'lodash/get';
import has from 'lodash/has';
import PropTypes from 'prop-types';
import React from 'react';
import { connectConfig } from '../lib/sn-component-workspace-config';
import { createLog } from '../utils/Logger';
import SearchSourceErrors from '../utils/SearchSourceErrors';
import {
    EXACT_MATCH_FETCH,
    WORKSPACE_TEXT_SEARCH,
    SN_WS_SEARCH_TAB_SEARCH_SUCCEEDED,
} from './actions';
import { ENUM_TAB_MODES } from './constants';
import NoResults from './GlobalSearchResult/NoResults';
import Overview from './GlobalSearchResult/Overview';
import { exactMatchDataShape } from './propTypes';

const LOG = createLog('GlobalSearchTab Overview');

export class ComponentWorkspaceGlobalSearchTabOverview extends React.PureComponent {
    constructor(props) {
        super(props);
        this.readInChunks = this.readInChunks.bind(this);
    }

    componentDidMount() {
        const { term, itemsToShowPerSection, searchSources } = this.props;
        this.readInChunks(term, itemsToShowPerSection, searchSources);
    }

    componentDidUpdate(prevProps) {
        const {
            term,
            itemsToShowPerSection,
            mode,
            searchSources,
            searchErrors,
            resetOverviewState,
            helpers: { dispatch },
        } = this.props;

        const currentTerm = term;
        const prevTerm = prevProps.term;
        const prevItemsToShowPerSection = prevProps.itemsToShowPerSection;

        if (
            (currentTerm && currentTerm !== prevTerm) ||
            itemsToShowPerSection !== prevItemsToShowPerSection
        ) {
            resetOverviewState();
            if (
                !this.getDataForTerm(currentTerm) ||
                itemsToShowPerSection !== prevItemsToShowPerSection
            )
                this.readInChunks(
                    currentTerm,
                    itemsToShowPerSection,
                    searchSources
                );
            return;
        }

        if (
            this.isChunkedReadComplete() &&
            searchSources.length &&
            mode === ENUM_TAB_MODES.OVERVIEW
        ) {
            const error = searchErrors.getAggregatedError(
                searchSources.length,
                LOG
            );

            dispatch(SN_WS_SEARCH_TAB_SEARCH_SUCCEEDED, {
                error,
                searchTerm: currentTerm,
            });

            if (error) {
                LOG.error(error);
            }
        }
    }

    getDataForTerm(term) {
        const { searchData } = this.props;
        if (searchData && searchData.term && searchData.term === term) {
            return searchData;
        }
        return undefined;
    }

    isSearching() {
        const { searchSources, term, searchErrors } = this.props;
        const searchData = this.getDataForTerm(term);
        const sourceList = get(searchData, 'sourceList', []);
        return searchSources.length !== sourceList.length + searchErrors.length;
    }

    readInChunks(sysParamTerm, sysParamLimit, sources, sysParamPage = 1) {
        const {
            exactMatchEnabled,
            helpers: { dispatch },
        } = this.props;
        if (sysParamTerm.length === 0) return;

        if (exactMatchEnabled) {
            dispatch(EXACT_MATCH_FETCH, {
                term: sysParamTerm,
            });
        }

        sources.forEach(source => {
            dispatch(
                WORKSPACE_TEXT_SEARCH,
                {
                    sys_id: source.sys_id,
                    sysparm_limit: sysParamLimit,
                    sysparm_page: sysParamPage,
                    sysparm_term: sysParamTerm,
                },
                {
                    searchParams: {
                        sys_id: source.sys_id,
                        sysparm_term: sysParamTerm,
                    },
                }
            );
        });
    }

    isChunkedReadComplete() {
        const { exactMatchData } = this.props;

        const isFetching = exactMatchData && exactMatchData.isFetching;
        return !this.isSearching() && !isFetching;
    }

    render() {
        const {
            exactMatchData,
            term,
            searchSources,
            onRecordSelect,
            onSectionDrillDown,
            searchData,
            handleSidebarItemClick,
            scrollIntoViewSection,
            mode,
            hideResultsCount,
        } = this.props;
        const hasExactMatch = exactMatchData && !!exactMatchData.data;

        if (!term) {
            LOG.debug(
                'Component was rendered without passing a term attribute.'
            );

            return <NoResults term="" />;
        }

        if (this.isChunkedReadComplete() && !hasExactMatch) {
            if (!searchSources.length) {
                LOG.debug('No search sources found');
                return <NoResults term={term} />;
            }

            if (!has(searchData, 'sourceList')) {
                LOG.debug('Component failed to retrieve API response.');
                return <NoResults term={term} />;
            }
        }

        const data = this.getDataForTerm(term);

        const recordCount = get(data, 'responseRecordCount', 0);
        if (recordCount < 1 && this.isChunkedReadComplete() && !hasExactMatch)
            return <NoResults term={term} />;

        const isOverviewVisible = mode === ENUM_TAB_MODES.OVERVIEW;
        return (
            <Overview
                term={term}
                scrollIntoViewSection={scrollIntoViewSection}
                reading={!this.isChunkedReadComplete()}
                isOverviewVisible={isOverviewVisible}
                data={data}
                onSidebarItemClick={handleSidebarItemClick}
                onRecordSelect={onRecordSelect}
                onSectionDrillDown={onSectionDrillDown}
                exactMatchData={exactMatchData}
                hideResultsCount={hideResultsCount}
            />
        );
    }
}

ComponentWorkspaceGlobalSearchTabOverview.propTypes = {
    term: PropTypes.string,
    itemsToShowPerSection: PropTypes.number.isRequired,
    searchSources: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
    exactMatchEnabled: PropTypes.bool.isRequired,
    hideResultsCount: PropTypes.bool,
    searchData: PropTypes.shape({
        term: PropTypes.string,
        responseRecordCount: PropTypes.number,
        sourceList: PropTypes.array,
        sources: PropTypes.object,
    }),
    searchErrors: PropTypes.shape({
        errors: PropTypes.array,
        getAggregatedError: PropTypes.func,
        length: PropTypes.number,
    }),
    onRecordSelect: PropTypes.func.isRequired,
    onSectionDrillDown: PropTypes.func.isRequired,
    scrollIntoViewSection: PropTypes.string,
    mode: PropTypes.string.isRequired,

    exactMatchData: exactMatchDataShape,
    helpers: PropTypes.func.isRequired,
    resetOverviewState: PropTypes.func.isRequired,
    handleSidebarItemClick: PropTypes.func.isRequired,
    dispatch: PropTypes.func.isRequired,
};

ComponentWorkspaceGlobalSearchTabOverview.defaultProps = {
    exactMatchData: undefined,
    scrollIntoViewSection: '',
    term: '',
    searchData: undefined,
    searchErrors: new SearchSourceErrors(),
    hideResultsCount: false,
};

function mapConfigToProps(config) {
    return {
        itemsToShowPerSection:
            config.global_search_config.tab_overview_items_per_section,
        searchSources: config.search_sources,
        exactMatchEnabled: config.global_search_config.exact_match_enabled,
        hideResultsCount: config.global_search_config.hide_search_results_count,
    };
}

export default connectConfig(mapConfigToProps)(
    ComponentWorkspaceGlobalSearchTabOverview
);
