export default userPreferences => {
    let recentActions;

    if (userPreferences) {
        if (
            userPreferences.name === 'workspace.globalSearch.recentActions' &&
            userPreferences.value
        ) {
            const rawData = userPreferences.value;
            try {
                recentActions = JSON.parse(rawData);
            } catch (e) {
                // continue regardless of error
            }
        }
    }

    return recentActions;
};
