/**
 *  Used to serialize label-value items into screen readable text
 * @param {string} recordTitle - Representing the title of the record to be announced
 * @param {Object[]} valueItems - Array of value items
 * @param {string} valueItems[].label - The label for the value item.
 * @param {Object} valueItems[].value - The value object for the value item.
 * @param {string} valueItems[].value.value - The value for the value item.
 */
const getAccessibleDisplayValueMessage = (recordTitle, valueItems) =>
    valueItems.reduce((prev, item) => {
        const itemSerialized = `${item.label.replace('_', ' ')}: ${
            item.value.value
        }`;
        if (!prev) return itemSerialized;
        return `${prev}, ${itemSerialized}`;
    }, recordTitle || '');

export default getAccessibleDisplayValueMessage;
