import { createCustomElement } from '@servicenow/ui-core';
import react from '@servicenow/ui-renderer-react';
import rtlBehavior from '@servicenow/behavior-rtl';
import {
    GLOBAL_SEARCH_CONFIG,
    presourceFactory,
    SEARCH_SOURCE,
    USER_PREFERENCE,
} from '../behaviors';
import SearchSourceErrors from '../utils/SearchSourceErrors';
import ComponentWorkspaceGlobalSearchTab from './ComponentWorkspaceGlobalSearchTab';
import actionHandlers from './actionHandlers';
import style from './_componentWorkspaceGlobalSearchTab.scss';

const ELEMENT_NAME = 'sn-component-workspace-global-search-tab';
/**
 * Global search tab allows users to view and drill down on the search results by sources.
 *
 * ```jsx
 * <sn-component-workspace-global-search-tab
 *      globalSearchViewConfigId="97a574ea53c0130084acddeeff7b12a6"
 *      globalSearchDataConfigId="0ce0997f7320001052c7d5fdbdf6a746"
 *      term="mac"
 *      hideSearchResultsCount={false}
 * />
 * ```
 *
 * @seismicElement sn-component-workspace-global-search-tab
 * @summary Seismic component that allows users to view and drill down on the search results by sources.
 */
export default createCustomElement(ELEMENT_NAME, {
    view: ComponentWorkspaceGlobalSearchTab,
    behaviors: [
        presourceFactory.get(SEARCH_SOURCE),
        presourceFactory.get(GLOBAL_SEARCH_CONFIG),
        presourceFactory.get(USER_PREFERENCE),
        { behavior: rtlBehavior },
    ],
    renderer: { type: react },
    initialComponentState: {
        page: 1,
        searchErrors: new SearchSourceErrors(),
        workspaceConfigs: null, // populated via gql if not legacy workspace
        isFetchingWorkspaceConfig: false,
    },
    properties: {
        /**
         * Term used to perform the search.
         * @type {string}
         */
        term: {
            default: '',
        },
        /**
         * Reference to a record in sys_search_context_config. Contains metadata about which search sources to use
         * @type {string}
         */
        globalSearchDataConfigId: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Reference to a record in sys_aw_global_search_config. Contains view configurations for the component.
         * @type {string}
         */
        globalSearchViewConfigId: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Disable the search results count from displaying
         * @type {boolean}
         */
        hideSearchResultsCount: {
            default: false,
            schema: {
                type: 'boolean',
            },
        },
        /**
         * Unique instance ID (optional)
         * @type {string}
         */
        instanceId: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Computed property for component to know where it's being rendered - UIB or legacy workspaces.
         */
        isLegacyWorkspace: {
            computed({
                properties: {
                    globalSearchDataConfigId,
                    globalSearchViewConfigId,
                },
            }) {
                return !globalSearchDataConfigId && !globalSearchViewConfigId;
            },
        },
    },
    styles: [style],
    actionHandlers,
    dispatches: {
        /**
         * Dispatched when a record is clicked from the results tab.
         * @type {{table: string, sysId: string}}
         */
        'SN_COMPONENT_WORKSPACE_GLOBAL_SEARCH_TAB#RECORD_CLICKED': {
            schema: {
                type: 'object',
                properties: {
                    table: { type: 'string' },
                    sysId: { type: 'string' },
                },
            },
        },
        /**
         * Dispatched when the search has successfully finished. This action is dispatched regardless
         * if results were returned or not and the payload will contain an error if there was an error during the search.
         * The payload also includes the search term used for the search.
         * @type {{error: object, searchTerm: string}}
         */
        'SN_COMPONENT_WORKSPACE_GLOBAL_SEARCH_TAB#SEARCH_SUCCEEDED': {
            schema: {
                type: 'object',
                properties: {
                    error: { type: 'object' },
                    searchTerm: { type: 'string' },
                },
            },
        },
    },
});
