import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
    USER_PREFERENCES_FETCH_STARTED,
    USER_PREFERENCES_FETCH_SUCCEEDED,
    USER_PREFERENCES_FETCH_FAILED,
} from '../../actions';

const userPreferenceQuery = `query($name: [String]!){
    GlideDomain_Query {
        user {
            preferences(name: $name) {
                name,
                value
            }
        }
    }
}`;

const handleTypeaheadUserPreferenceFetch = createGraphQLEffect(
    userPreferenceQuery,
    {
        variableList: ['name'],
        startActionType: USER_PREFERENCES_FETCH_STARTED,
        successActionType: USER_PREFERENCES_FETCH_SUCCEEDED,
        errorActionType: USER_PREFERENCES_FETCH_FAILED,
    }
);

export default handleTypeaheadUserPreferenceFetch;
