import React from 'react';
import PropTypes from 'prop-types';
import isEmpty from 'lodash/isEmpty';
import '@servicenow/now-heading';

import FieldList from './FieldList';

const DefaultCard = props => {
    const {
        onSelect,
        description,
        displayFields,
        title,
        showDescription,
        sysId,
    } = props;

    const descriptionId = `fieldListDescription_${sysId}`;

    return (
        <li
            className="record default"
            onClick={onSelect}
            onKeyDown={onSelect}
            data-testclass="sn-global-search-record"
        >
            <a
                href="javascript:void(0)" // eslint-disable-line no-script-url
                aria-describedby={descriptionId}
            >
                <now-heading
                    has-no-margin
                    label={title}
                    level="3"
                    variant="title-primary"
                />
            </a>
            <FieldList
                descriptionId={descriptionId}
                fields={displayFields}
                spreadVertically={false}
            />
            {showDescription && !isEmpty(description) && (
                <div className="sn-list-group--content sn-list-group--content__description">
                    <span>{description}</span>
                </div>
            )}
        </li>
    );
};

DefaultCard.propTypes = {
    onSelect: PropTypes.func.isRequired,
    title: PropTypes.string,
    description: PropTypes.string,
    displayFields: PropTypes.array, // eslint-disable-line react/forbid-prop-types
    showDescription: PropTypes.bool,
    sysId: PropTypes.string.isRequired,
};

DefaultCard.defaultProps = {
    title: '',
    description: '',
    displayFields: [],
};

DefaultCard.defaultProps = {
    showDescription: true,
};

export default DefaultCard;
