import find from 'lodash/find';
import merge from 'lodash/merge';
import get from 'lodash/get';

const transformAResponse = (responseData, metadata) => {
    const rawResult = get(responseData, 'result');
    if (!rawResult) return {};

    const {
        result_count: resultCount = 0,
        search_results: searchResults = [],
        sources = [],
        term = '',
    } = rawResult;

    const transformedSources = {};
    const sourceList = [];

    sources.forEach(source => {
        const {
            sys_id: sysId,
            name,
            condition,
            source_table: sourceTable,
        } = source;
        const sectionSearchResults = find(searchResults, { sys_id: sysId });
        const {
            records,
            record_count: recordCount,
            fields,
        } = sectionSearchResults;

        if (sectionSearchResults) {
            sourceList.push(sysId);
            transformedSources[sysId] = {
                name: name.display,
                condition,
                databaseRecordCount: recordCount,
                tableName: sourceTable,
                records,
                displayFields: fields,
            };
        }
    });

    if (!transformedSources || sourceList.length === 0 || !term) return {};

    return {
        sources: transformedSources,
        sourceList,
        responseRecordCount: resultCount,
        term,
        metadata,
    };
};

const mergeGlobalSearchResponses = (currentData, responseData) => {
    const { term } = currentData;

    // current data - A
    const sourcesA = currentData.sources;
    const sourceListA = currentData.sourceList;
    const responseRecordCountA = currentData.responseRecordCount;
    const { metadata } = currentData;

    const transformedResponse = transformAResponse(responseData, metadata);
    // response - B
    const sourcesB = transformedResponse.sources;
    const sourceListB = transformedResponse.sourceList;
    const responseRecordCountB = transformedResponse.responseRecordCount;

    // merging
    const sources = merge(sourcesA, sourcesB);
    const sourceList = Array.prototype.concat(sourceListA, sourceListB);

    const responseRecordCount = responseRecordCountA + responseRecordCountB;

    return {
        sources,
        sourceList,
        responseRecordCount,
        term,
        metadata,
    };
};

const transformTextSearchOverview = (currentData, result, requestMetadata) => {
    // this is the first response
    const {
        globalSearchViewConfigId,
        globalSearchDataConfigId,
    } = requestMetadata;
    const meta = { globalSearchViewConfigId, globalSearchDataConfigId };

    if (!currentData) return transformAResponse(result, meta);

    const {
        term: currentTerm,
        metadata: {
            globalSearchViewConfigId: prevGlobalSearchViewConfigId,
            globalSearchDataConfigId: prevGlobalSearchDataConfigId,
        },
    } = currentData;

    const { term: resultTerm } = result.result;

    // new search, use new results
    if (
        currentTerm !== resultTerm ||
        globalSearchViewConfigId !== prevGlobalSearchViewConfigId ||
        globalSearchDataConfigId !== prevGlobalSearchDataConfigId
    ) {
        return transformAResponse(result, meta);
    }

    return mergeGlobalSearchResponses(currentData, result);
};

export default transformTextSearchOverview;
