import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
    DRILLDOWN_FETCH_FAILED,
    DRILLDOWN_FETCH_STARTED,
    DRILLDOWN_FETCH_SUCCEEDED,
} from '../../actions';

const handleDrilldownFetchEffect = createHttpEffect(
    '/api/now/v1/search/sources/:sys_id/textsearch',
    {
        method: 'GET',
        headers: {},
        pathParams: ['sys_id'],
        queryParams: ['sysparm_limit', 'sysparm_page', 'sysparm_term'],
        startActionType: DRILLDOWN_FETCH_STARTED,
        successActionType: DRILLDOWN_FETCH_SUCCEEDED,
        errorActionType: DRILLDOWN_FETCH_FAILED,
    }
);

export default handleDrilldownFetchEffect;
