import slice from 'lodash/slice';
import has from 'lodash/has';
import cloneDeep from 'lodash/cloneDeep';

import GlobalSearchModel from './GlobalSearchModel';

const RECENT_ACTION_SECTIONS = {
    ITEM_SELECTED: 'ITEM_SELECTED',
    TERM_SEARCHED: 'TERM_SEARCHED',
};

const MILLISECONDS_IN_A_DAY = 86400000;
// this is based on the "max length" dictionary entry for value column in the user preference table
// there is an assumption here that the platform counts the number of utf8 chars and not the number of bytes
const DATA_STORAGE_LIMIT_IN_UTF8_CHARACTERS = 65000;

class RecentActionsModel extends GlobalSearchModel {
    static get SECTIONS() {
        return RECENT_ACTION_SECTIONS;
    }

    static createNewRecentAction(section, actionRecord, meta = {}) {
        if (section === RecentActionsModel.SECTIONS.ITEM_SELECTED) {
            const { table, sys_id: sysId } = actionRecord;
            if (!table || !sysId) return null;
            return {
                record: { table, sys_id: sysId },
                ...meta,
                created: Date.now(),
            };
        }
        if (section === RecentActionsModel.SECTIONS.TERM_SEARCHED) {
            if (typeof actionRecord !== 'string') return null;
            return {
                term: actionRecord,
                created: Date.now(),
            };
        }
        return undefined;
    }

    constructor(
        response = { ITEM_SELECTED: [], TERM_SEARCHED: [] },
        maxItems = 4,
        maxTerms = 4,
        actionsTTLInDays = 30,
        recentlySearchedLabel = '',
        recentlyViewedLabel = ''
    ) {
        super();
        this.maxItems = maxItems;
        this.maxTerms = maxTerms;
        this.actionsTTLInDays = actionsTTLInDays;
        this.recentlySearchedLabel = recentlySearchedLabel;
        this.recentlyViewedLabel = recentlyViewedLabel;
        this.itemData = [];
        this.parse(response);
        // helps with the tests
        this.DATA_STORAGE_LIMIT_IN_UTF8_CHARACTERS = DATA_STORAGE_LIMIT_IN_UTF8_CHARACTERS;
    }

    parse(recentActions) {
        this.rawData = recentActions;

        this.itemSelectedRecords = this.parseItemSelectedActions(
            recentActions.ITEM_SELECTED
        );
        this.termSearchedRecords = this.parseTermSearchedActions(
            recentActions.TERM_SEARCHED
        );

        this.sources = [
            {
                name: this.recentlySearchedLabel,
                id: RecentActionsModel.SECTIONS.TERM_SEARCHED,
            },
            {
                name: this.recentlySearchedViewedLabel,
                id: RecentActionsModel.SECTIONS.ITEM_SELECTED,
            },
        ];

        this.resultSectionsRecordLength = [
            this.termSearchedRecords.length,
            this.itemSelectedRecords.length,
        ];
        this.resultCount =
            this.itemSelectedRecords.length + this.termSearchedRecords.length;
        this.totalResults = this.resultCount;

        this.mapSourceIdToSourceData[
            RecentActionsModel.SECTIONS.ITEM_SELECTED
        ] = {
            record_count: this.itemSelectedRecords.length,
            name: this.recentlyViewedLabel,
            records: [],
        };
        this.mapSourceIdToSourceData[
            RecentActionsModel.SECTIONS.TERM_SEARCHED
        ] = {
            record_count: this.termSearchedRecords.length,
            name: this.recentlySearchedLabel,
            records: [],
        };

        this.itemSelectedRecords.forEach(recordObj => {
            const { table, __sourceId } = recordObj;
            this.mapTableNameToSourceId[table] = __sourceId;
            this.mapSourceIdToSourceData[
                RecentActionsModel.SECTIONS.ITEM_SELECTED
            ].records.push(recordObj);
        });
    }

    get data() {
        return Object.assign({}, this.rawData);
    }

    filterDeadActions() {
        const now = Date.now();
        const TTLInMS = this.actionsTTLInDays * MILLISECONDS_IN_A_DAY;
        return action => {
            if (has(action, 'created')) {
                if (now - action.created < TTLInMS) return true;
                return false;
            }
            return false;
        };
    }

    get sectionsFilteredByResponseCount() {
        return this.sources
            .filter(
                source =>
                    this.getSectionRecordsBySectionId(source.id).length > 0
            )
            .map(section => section.id);
    }

    getSectionRecordsBySectionId(sourceId) {
        if (sourceId === RecentActionsModel.SECTIONS.ITEM_SELECTED)
            return this.itemSelectedRecords;
        if (sourceId === RecentActionsModel.SECTIONS.TERM_SEARCHED)
            return this.termSearchedRecords;
        return undefined;
    }

    parseItemSelectedActions(actionRecords) {
        return slice(actionRecords, 0, this.maxItems)
            .filter(this.filterDeadActions())
            .map(actionRecord => {
                const { record } = { ...actionRecord };

                if (record && actionRecord.sourceId) {
                    record.__sourceId = actionRecord.sourceId;
                    return record;
                }
                return actionRecord.record;
            });
    }

    parseTermSearchedActions(actionRecords) {
        return slice(actionRecords, 0, this.maxTerms)
            .filter(this.filterDeadActions())
            .map(actionRecord => {
                if (typeof actionRecord === 'object')
                    return actionRecord.term || '';
                if (typeof actionRecord === 'string') return actionRecord || '';
                return undefined;
            });
    }

    addRecentAction(section, actionData, metadata) {
        const recentActions = this.data;
        if (section === RecentActionsModel.SECTIONS.ITEM_SELECTED) {
            recentActions.ITEM_SELECTED = recentActions.ITEM_SELECTED.filter(
                action => action.record.sys_id !== actionData.sys_id
            );
            const actionRecord = RecentActionsModel.createNewRecentAction(
                section,
                actionData,
                metadata
            );
            if (actionRecord) {
                recentActions.ITEM_SELECTED.unshift(actionRecord);
                if (recentActions.ITEM_SELECTED.length > this.maxItems) {
                    recentActions.ITEM_SELECTED = slice(
                        recentActions.ITEM_SELECTED,
                        0,
                        this.maxItems
                    );
                }
            }
        } else if (section === RecentActionsModel.SECTIONS.TERM_SEARCHED) {
            recentActions.TERM_SEARCHED = recentActions.TERM_SEARCHED.filter(
                action => action.term !== actionData
            );
            const actionRecord = RecentActionsModel.createNewRecentAction(
                section,
                actionData,
                metadata
            );
            if (actionRecord) {
                recentActions.TERM_SEARCHED.unshift(actionRecord);
                if (recentActions.TERM_SEARCHED.length > this.maxTerms) {
                    recentActions.TERM_SEARCHED = slice(
                        recentActions.TERM_SEARCHED,
                        0,
                        this.maxTerms
                    );
                }
            }
        } else {
            // if section is none of what we support
            return '';
        }

        const stringifiedJsonObject = this.prepareDataForStorage(
            recentActions,
            section
        );
        return stringifiedJsonObject;
    }

    // todo: handle .length to take care of utf8 characters
    dataCanBeStored(data) {
        return data.length < this.DATA_STORAGE_LIMIT_IN_UTF8_CHARACTERS;
    }

    prepareDataForStorage(_recentActions, targetSection) {
        const recentActions = cloneDeep(_recentActions);
        let stringifiedData = JSON.stringify(recentActions);
        // try to popout older items 3 times from the target section
        for (let i = 0; i < 3; i += 1) {
            // if data can be stored, return
            if (this.dataCanBeStored(stringifiedData)) {
                return stringifiedData;
            }
            recentActions[targetSection].pop();
            stringifiedData = JSON.stringify(recentActions);
        }

        // return if data can now be stored after 3rd attempt to popoutExcess
        if (this.dataCanBeStored(stringifiedData)) {
            return stringifiedData;
        }
        // if it still cannot be stored, clear the section completely and keep the most recently added data
        recentActions[targetSection] = [recentActions[targetSection].shift()];
        stringifiedData = JSON.stringify(recentActions);

        if (this.dataCanBeStored(stringifiedData)) {
            return stringifiedData;
        }
        // if we still cannot store the data after clearing out the section, clear out everything and store nothing

        return JSON.stringify({ ITEM_SELECTED: [], TERM_SEARCHED: [] });
    }
}

export default RecentActionsModel;
