/**
 * Utils for ComponentWorkspaceGlobalSearchTypeahead
 */

import get from 'lodash/get';

export const isExactMatchPresent = (props, term) => {
    if (term === get(props, 'exactMatch.term'))
        return get(props, 'exactMatch.hasExactMatch');

    return false;
};

export const canTermMatchExactly = (term, criterionRegex, exactMatchEnabled) =>
    exactMatchEnabled && new RegExp(criterionRegex).test(term);

export const hasExactMatchReadingFinished = (
    props,
    term,
    termCriterionRegex,
    exactMatchEnabled
) => {
    const { exactMatch } = props;
    if (!exactMatch) return true;

    const { isFetching } = exactMatch;
    // if we are not reading, we have either not started reading yet, or already finished reading
    if (!isFetching) {
        const dataTerm = get(props, 'exactMatch.term');
        if (
            // if the dataTerm !== term, reading has not started
            dataTerm !== term &&
            //  make sure reading hasn't started only because we are never going to start it
            canTermMatchExactly(term, termCriterionRegex, exactMatchEnabled)
        ) {
            return false;
        }
    }

    return !isFetching;
};
