import React from 'react';
import PropTypes from 'prop-types';
import intersectionBy from 'lodash/intersectionBy';
import differenceBy from 'lodash/differenceBy';
import { t } from 'sn-translate';
import FieldList from './FieldList';

const LOCATION_SUB_FIELDS = [
    'street',
    'city',
    'state',
    'country',
    'zip',
    'latitude',
    'longitude',
].map(value => ({ name: value }));

export const getNonLocationFields = fields =>
    differenceBy(fields, LOCATION_SUB_FIELDS, 'name');

const LocationField = props => {
    const { subFields, descriptionId } = props;

    const locationSubFields = intersectionBy(
        subFields,
        LOCATION_SUB_FIELDS,
        'name'
    );

    const locationValue = locationSubFields
        .map(field => field.value)
        .filter(value => !!value)
        .join(' | ');

    return (
        <FieldList
            descriptionId={descriptionId}
            fields={[
                {
                    label: t('Location'),
                    value: locationValue,
                    name: 'location',
                },
            ]}
        />
    );
};

LocationField.propTypes = {
    subFields: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
    descriptionId: PropTypes.string.isRequired,
};

export default LocationField;
