const handleTypeaheadUserPreferenceSetStarted = ({
    state,
    updateState,
    action,
}) => {
    const updatedUserPreferences = { ...state.userPreferences };

    if (action.payload.action === 'ITEM_SELECTED') {
        updatedUserPreferences.lastSysIdAddedAsRecentAction =
            action.payload.sysId;
    }

    if (action.payload.action === 'TERM_SEARCHED') {
        updatedUserPreferences.lastTermAddedAsRecentAction =
            action.payload.term;
    }

    updateState({
        ...state,
        fetching: true,
        userPreferences: updatedUserPreferences,
    });
};

export default handleTypeaheadUserPreferenceSetStarted;
