import React from 'react';
import PropTypes from 'prop-types';
import take from 'lodash/take';

import Thumbnail from './Thumbnail';
import FieldList from './FieldList';
import LocationField, { getNonLocationFields } from './LocationField';

const MAX_NON_LOCATION_FIELDS_TO_SHOW = 2;

const CompanyCard = props => {
    const { onSelect, displayFields, thumbnail, title, sysId } = props;

    const nonLocationFields = take(
        getNonLocationFields(displayFields),
        MAX_NON_LOCATION_FIELDS_TO_SHOW
    );

    const descriptionId = `fieldListDescription_${sysId}`;

    return (
        <div
            className="sn-list-group--item sn-list-group--item__grid"
            onClick={onSelect}
            onKeyDown={onSelect}
            role="link"
            tabIndex={0}
            data-testclass="sn-global-search-record"
        >
            <div className="sn-global-search--content sn-global-search--content__static">
                {/* {TODO: This should be a building icon when design is finished creating it} */}
                <Thumbnail type="circle" url={thumbnail} icon="buildingFill" />
            </div>
            <div className="sn-global-search--content">
                <span className="sn-list-group--item--title">
                    <span>{title}</span>
                </span>
                <FieldList
                    fields={nonLocationFields}
                    descriptionId={descriptionId}
                />
                <LocationField
                    descriptionId={descriptionId}
                    subFields={displayFields}
                />
            </div>
        </div>
    );
};

CompanyCard.propTypes = {
    onSelect: PropTypes.func.isRequired,
    title: PropTypes.string.isRequired,
    displayFields: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
    sysId: PropTypes.string.isRequired,
    thumbnail: PropTypes.string,
};

CompanyCard.defaultProps = {
    thumbnail: undefined,
};

export default CompanyCard;
