import React from 'react';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import debounce from 'lodash/debounce';

import DrillDown from './GlobalSearchResult/DrillDownView';
import GlobalSearchUtils from '../utils/GlobalSearchUtils';
import { connectConfig } from '../lib/sn-component-workspace-config';
import { createLog } from '../utils/Logger';
import { drilldownDataShape } from './propTypes';
import { DRILLDOWN_FETCH } from './actions';

const LOG = createLog('GlobalSearchTab DrillDown');

export class ComponentWorkspaceGlobalSearchTabDrillDown extends React.PureComponent {
    constructor(props) {
        super(props);

        this.debouncedRead = debounce(this.read, props.paginationDebounce, {
            leading: true,
        });
        this.switchToOverviewMode = this.switchToOverviewMode.bind(this);
    }

    componentDidMount() {
        const { page } = this.props;

        const {
            helpers: { dispatch },
            drilledDownSection: sourceId,
            term,
            drilldownData,
        } = this.props;

        if (
            drilldownData &&
            drilldownData.term === term &&
            drilldownData.sourceSysId === sourceId
        ) {
            // if the data present is what we are looking for, return without actually sending out the query
            return;
        }

        GlobalSearchUtils.dispatchPageUpdate(dispatch, {
            sourceId,
            term,
            page: 1,
        });

        this.debouncedRead(sourceId, term, page);
    }

    componentWillReceiveProps(nextProps) {
        const {
            drilledDownSection: sourceId,
            page,
            paginationDebounce,
            term,
        } = this.props;

        if (paginationDebounce !== nextProps.paginationDebounce) {
            this.debouncedRead = debounce(
                this.read,
                nextProps.paginationDebounce,
                {
                    leading: true,
                }
            );
        }

        const {
            drilledDownSection: nextSourceId,
            page: nextPage,
            term: nextTerm,
        } = nextProps;

        if (
            nextPage !== page ||
            nextSourceId !== sourceId ||
            nextTerm !== term
        ) {
            this.debouncedRead(nextSourceId, nextTerm, nextPage);
        }
    }

    componentDidUpdate(prevProps) {
        const error = get(this.props, 'drilldownData.error');
        const prevError = get(prevProps, 'drilldownData.error');
        if (error && error !== prevError) {
            LOG.debug(
                'Unexpected error while fetching the requested page. Error object : ',
                error
            );
        }
    }

    switchToOverviewMode() {
        const { handleBackToResultsClick } = this.props;
        handleBackToResultsClick();
    }

    read(sysId, sysParmTerm, sysParamPage) {
        // Set reading to true then fetch data
        const {
            helpers: { dispatch },
            itemsToShowPerPage: sysparmLimit,
        } = this.props;
        dispatch(
            DRILLDOWN_FETCH,
            {
                sys_id: sysId,
                sysparm_limit: sysparmLimit,
                sysparm_page: sysParamPage,
                sysparm_term: sysParmTerm,
            },
            { sysId, sysParmTerm }
        );
    }

    render() {
        const {
            drilldownData,
            drilledDownSection,
            drilledDownSectionTitle,
            onRecordSelect,
            page,
            term,
        } = this.props;

        return (
            <DrillDown
                data={drilldownData}
                currentPage={page}
                onRecordSelect={onRecordSelect}
                resetDrillDownState={this.switchToOverviewMode}
                term={term}
                sectionId={drilledDownSection}
                sectionTitle={drilledDownSectionTitle}
            />
        );
    }
}

ComponentWorkspaceGlobalSearchTabDrillDown.propTypes = {
    term: PropTypes.string,
    onRecordSelect: PropTypes.func.isRequired,
    itemsToShowPerPage: PropTypes.number.isRequired,
    paginationDebounce: PropTypes.number.isRequired,
    page: PropTypes.number,
    drilldownData: drilldownDataShape,
    helpers: PropTypes.func.isRequired,
    handleBackToResultsClick: PropTypes.func.isRequired,
    drilledDownSection: PropTypes.string.isRequired,
    drilledDownSectionTitle: PropTypes.string.isRequired,
};

ComponentWorkspaceGlobalSearchTabDrillDown.defaultProps = {
    drilldownData: undefined,
    page: 1,
    term: '',
};

function mapConfigToProps(config) {
    return {
        itemsToShowPerPage:
            config.global_search_config.tab_drilldown_items_per_page,
        paginationDebounce: config.global_search_config.tab_pagination_debounce,
    };
}

export default connectConfig(mapConfigToProps)(
    ComponentWorkspaceGlobalSearchTabDrillDown
);
