import React from 'react';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import { t } from 'sn-translate';

import '@servicenow/now-heading';
import '@servicenow/now-icon';
import '@servicenow/now-tooltip';
import '@servicenow/now-text-link';

import ResultRecords from './ResultRecords';
import { connectConfig } from '../../lib/sn-component-workspace-config';

export class Section extends React.PureComponent {
    constructor(props) {
        super(props);

        this.infoIconRef = null;
        this.nodeRef = null;
        this.linkRef = null;
        this.getInfoIconRef = this.getInfoIconRef.bind(this);
        this.getNodeRef = this.getNodeRef.bind(this);
        this.getLinkRef = this.getLinkRef.bind(this);
        this.onViewAllClick = this.onViewAllClick.bind(this);
    }

    componentDidMount() {
        const { focusLinkOnMount } = this.props;
        if (focusLinkOnMount && this.linkRef) {
            this.linkRef.focus();
        }
    }

    componentDidUpdate() {
        const { scrollSectionIntoView } = this.props;
        if (scrollSectionIntoView && this.nodeRef) {
            this.nodeRef.scrollIntoView(true);
        }
    }

    onViewAllClick(event) {
        const { onViewAllClick } = this.props;
        event.preventDefault();
        if (this.isDrillDownViewAvailable && onViewAllClick) {
            const sectionSysId = get(this.props, 'section');
            const sectionTitle = get(this.props, 'data.name');
            onViewAllClick(sectionSysId, sectionTitle);
        }
    }

    get isDrillDownViewAvailable() {
        const { data, limitPerSource } = this.props;
        return data.databaseRecordCount > limitPerSource;
    }

    getInfoIconRef(el) {
        this.infoIconRef = el;
    }

    getNodeRef(el) {
        this.nodeRef = el;
    }

    getLinkRef(el) {
        this.linkRef = el;
    }

    render() {
        const {
            data,
            section,
            onRecordSelect,
            limitPerSource,
            hideResultsCount,
        } = this.props;

        const {
            name,
            condition,
            databaseRecordCount,
            tableName,
            records,
            displayFields,
        } = data;

        const visibleCount =
            records.length >= limitPerSource ? limitPerSource : records.length;

        const sectionHeaderText = hideResultsCount
            ? name
            : t('{0} ({1} of {2})', name, visibleCount, databaseRecordCount);

        const labelId = `section-${section}`;

        return (
            <div
                className="global-search-records global-search-section"
                ref={this.getNodeRef}
            >
                <header className="global-search-content--header">
                    <div className="global-search-content">
                        <now-heading
                            id={labelId}
                            has-no-margin
                            label={sectionHeaderText}
                            level="2"
                            variant="title-primary"
                        />
                        {condition && condition.value && (
                            <div className="global-search-content--tooltip">
                                <now-icon
                                    aria-describedby={`tooltip-${section}`}
                                    icon="infoCircleOutline"
                                    size="sm"
                                    ref={this.getInfoIconRef}
                                />
                                <now-tooltip
                                    id={`tooltip-${section}`}
                                    content={condition.display}
                                    target-ref={this.infoIconRef}
                                />
                            </div>
                        )}
                    </div>
                    {databaseRecordCount > limitPerSource && (
                        <div className="sn-section--content sn-section--content__static">
                            <a
                                className="sn-url"
                                href="javascript:void(0)" // eslint-disable-line no-script-url
                                onClick={this.onViewAllClick}
                            >
                                <now-text-link
                                    label={t('View all {0}', name)}
                                    variant="primary"
                                    size="md"
                                    // eslint-disable-next-line no-script-url
                                    href="javascript:void(0)"
                                />
                            </a>
                        </div>
                    )}
                </header>
                <div>
                    <ResultRecords
                        ariaLabelledBy={labelId}
                        fieldOrder={displayFields}
                        section={section}
                        sectionTableName={tableName}
                        records={records}
                        onRecordSelect={onRecordSelect}
                    />
                </div>
            </div>
        );
    }
}

Section.propTypes = {
    scrollSectionIntoView: PropTypes.bool,
    focusLinkOnMount: PropTypes.bool,
    section: PropTypes.string.isRequired,
    limitPerSource: PropTypes.number.isRequired,
    onRecordSelect: PropTypes.func.isRequired,
    onViewAllClick: PropTypes.func,
    // eslint-disable-next-line react/forbid-prop-types
    data: PropTypes.object.isRequired,
    hideResultsCount: PropTypes.bool,
};

Section.defaultProps = {
    focusLinkOnMount: false,
    scrollSectionIntoView: false,
    onViewAllClick: () => undefined,
    hideResultsCount: false,
};

function mapConfigToProps(config) {
    return {
        limitPerSource:
            config.global_search_config.tab_overview_items_per_section,
    };
}

export default connectConfig(mapConfigToProps)(Section);
