import React from 'react';
import PropTypes from 'prop-types';
import ResultRecord from '../TypeaheadPopover/ResultRecord';
import style from './_componentWorkspaceGlobalSearchTypeaheadItemRecordElement.scss';
import { TYPEAHEAD_ITEM_LOOKUP } from './actions';

class ComponentWorkspaceGlobalSearchTypeaheadItemRecord extends React.PureComponent {
    componentDidMount() {
        const {
            properties: { table, sysId },
        } = this.props;

        this.readItem(table, sysId);
    }

    componentWillReceiveProps(nextProps) {
        const {
            properties: { table, sysId },
        } = this.props;

        const {
            properties: { table: nextTable, sysId: nextSysId },
        } = nextProps;

        if (table !== nextTable || sysId !== nextSysId) {
            this.readItem(nextTable, nextSysId);
        }
    }

    readItem(table, sysId) {
        const {
            helpers: { dispatch },
        } = this.props;
        if (!table || !sysId) return;

        dispatch(TYPEAHEAD_ITEM_LOOKUP, {
            sysparm_table_name: table,
            sysparm_item_sys_id: sysId,
        });
    }

    render() {
        const { typeaheadItem } = this.props;

        if (typeaheadItem && !typeaheadItem.isFetching) {
            const { record, displayFields } = typeaheadItem;

            return (
                <ResultRecord record={record} displayFields={displayFields} />
            );
        }
        return null;
    }
}

ComponentWorkspaceGlobalSearchTypeaheadItemRecord.propTypes = {
    // eslint-disable-next-line react/forbid-prop-types
    typeaheadItem: PropTypes.object,
    helpers: PropTypes.func.isRequired,
    properties: PropTypes.shape({
        table: PropTypes.string,
        sysId: PropTypes.string,
    }).isRequired,
};

ComponentWorkspaceGlobalSearchTypeaheadItemRecord.defaultProps = {
    typeaheadItem: undefined,
};

ComponentWorkspaceGlobalSearchTypeaheadItemRecord.style = style;

export default ComponentWorkspaceGlobalSearchTypeaheadItemRecord;
