import get from 'lodash/get';
import SearchSourceErrors from '../../../utils/SearchSourceErrors';

const handleTextSearchStarted = ({ state, updateState, action }) => {
    const currentTerm = get(state, 'searchData.term');
    const nextTerm = get(action, 'meta.searchParams.sysparm_term');
    const searchErrors =
        currentTerm === nextTerm
            ? state.searchErrors
            : new SearchSourceErrors();
    updateState({
        ...state,
        searchErrors,
    });
};

export default handleTextSearchStarted;
