import get from 'lodash/get';
import { t } from 'sn-translate';

const transformTabExactMatchResponse = response => {
    const term = get(response, 'result.term');
    const record = get(response, 'result.search_results.0.records.0');
    const displayFields = get(response, 'result.search_results.0.fields');

    if (!term || !record || !displayFields) return undefined;

    return {
        term,
        sources: {
            EXACT_MATCH_SECTION: {
                name: t('Exact Match'),
                condition: '',
                databaseRecordCount: 1,
                records: [record],
                displayFields,
            },
        },
    };
};

export default transformTabExactMatchResponse;
