const createQuerySelector = querySelector => host =>
    host.shadowRoot.querySelector(querySelector);
const createQuerySelectorAll = querySelector => host =>
    host.shadowRoot.querySelectorAll(querySelector);

const getPopoverElements = createQuerySelectorAll('div.sn-popover');
const getInputContainerElement = createQuerySelector(
    'div.sn-global-typeahead-control-container'
);

const handleFitResize = ({ host }) => {
    const contentElems = getPopoverElements(host);
    const targetElem = getInputContainerElement(host);

    if (contentElems && contentElems.length > 0) {
        contentElems.forEach(contentElem => {
            const currentWidth = contentElem.style.width;
            const targetWidth = `${targetElem.offsetWidth}px`;

            const currentLeft = contentElem.style.left;
            const targetLeft = `${targetElem.offsetLeft}px`;

            const currentTop = contentElem.style.top;
            const targetTop = `${targetElem.offsetTop +
                targetElem.offsetHeight}px`;

            if (currentWidth !== targetWidth)
                Object.assign(contentElem.style, { width: targetWidth });

            if (currentLeft !== targetLeft)
                Object.assign(contentElem.style, { left: targetLeft });

            if (currentTop !== targetTop)
                Object.assign(contentElem.style, { top: targetTop });
        });
    }
};

export default handleFitResize;
