import { style as iconStyle } from '@servicenow/now-legacy-icon';
import get from 'lodash/get';
import merge from 'lodash/merge';
import omit from 'lodash/omit';
import trim from 'lodash/trim';
import PropTypes from 'prop-types';
import React from 'react';
import { getProperty } from 'sn-uxpage-presource';
import getBehaviorProp from '../behaviors/util/getBehaviorProp';
import { GLOBAL_SEARCH_CONFIG, USER_PREFERENCE } from '../behaviors';
import { ConfigProvider } from '../lib/sn-component-workspace-config';
import ErrorBoundary from '../utils/ErrorBoundary';
import GlobalSearchUtils from '../utils/GlobalSearchUtils';
import PresourceDefaults from '../utils/PresourceDefaults';
import {
    hasExactMatchReadingFinished,
    isExactMatchPresent,
} from '../utils/workspaceExactMatchTypeahead';
import { USER_PREFERENCES_FETCH, FIT_RESIZE } from './actions';
import ComponentExactMatch from './ComponentExactMatch';
import ComponentRecentActions from './ComponentRecentActions';
import ComponentTypeaheadInput from './ComponentTypeaheadInput/ComponentTypeaheadInput';
import { exactMatchDataShape, userPreferenceDataShape } from './propTypes';
import TypeaheadRecord from './TypeaheadPopover/TypeaheadRecord';
import style from './_componentWorkspaceGlobalSearchTypeahead.scss';

const GLOBAL_SEARCH_TYPEAHEAD_DEFAULT_LOCAL_PROPS = {
    typeaheadInputValue: '',
    dropdownVisible: false,
    searchExpanded: false,
    isDisabled: false,
};

const ENUM_TYPEAHEAD_MODES = {
    RESULTS: 'results',
    RECENT_ACTIONS: 'recent_actions',
    EXACT_MATCH: 'exact_match',
};

const USER_PREFERENCES_NAME = 'workspace.globalSearch.recentActions';

const configFromSysProperties = () => {
    const globalSearchConfig = {};
    const typeaheadDebounce = GlobalSearchUtils.getSysPropertyAsNumber(
        'com.snc.agent_workspace.global_search.typeahead.debounce'
    );
    if (typeaheadDebounce)
        globalSearchConfig.typeahead_debounce = typeaheadDebounce;

    const exactMatchRequestCriterionRegex = getProperty(
        'com.snc.agent_workspace.global_search.typeahead.exact_match_request_criterion_regex'
    );
    if (exactMatchRequestCriterionRegex)
        globalSearchConfig.exact_match_request_criterion_regex = exactMatchRequestCriterionRegex;

    return { global_search_config: globalSearchConfig };
};

const getGlobalSearchConfigPresourceData = props =>
    getBehaviorProp(props, GLOBAL_SEARCH_CONFIG);
const getUserPreferencePresourceData = props =>
    getBehaviorProp(props, USER_PREFERENCE);

class ComponentWorkspaceGlobalSearchTypeahead extends React.Component {
    static get DEFAULT_LOCAL_PROPS() {
        return GLOBAL_SEARCH_TYPEAHEAD_DEFAULT_LOCAL_PROPS;
    }

    static get MODES() {
        return ENUM_TYPEAHEAD_MODES;
    }

    constructor(props) {
        super(props);

        this.state = {
            navigateInDirection: '',
            hasActiveRecord: false,
            activeRecord: {},
            ariaSelected: {
                recordIndex: -1,
                sectionIndex: -1,
            },
            dropdownVisible: false,
            isDisabled: false,
            typeaheadInputValue: '',
            searchExpanded: false,
        };

        this.onSearchInputSubmit = this.onSearchInputSubmit.bind(this);
        this.onSearchInputEscapeKeyDown = this.onSearchInputEscapeKeyDown.bind(
            this
        );
        this.onTypeaheadRecordClick = this.onTypeaheadRecordClick.bind(this);
        this.onActiveRecordUpdate = this.onActiveRecordUpdate.bind(this);
        this.setNavigationDirection = this.setNavigationDirection.bind(this);
        this.clearActiveRecord = this.clearActiveRecord.bind(this);
        this.handleBlur = this.handleBlur.bind(this);
        this.handleFocus = this.handleFocus.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleSearchIconClick = this.handleSearchIconClick.bind(this);
        this.handleClearIconClick = this.handleClearIconClick.bind(this);
        this.handleGeneralKeyDown = this.handleGeneralKeyDown.bind(this);
        this.getResolvedConfig = this.getResolvedConfig.bind(this);
    }

    componentWillReceiveProps(nextProps) {
        const { isDisabled, typeaheadInputValue } = this.state;
        const { exactMatch: currExactMatch } = this.props;
        const { exactMatch: nextExactMatch } = nextProps;
        const currentlyFetching = currExactMatch && currExactMatch.isFetching;
        const nextFetching = nextExactMatch && nextExactMatch.isFetching;

        if (currentlyFetching && !nextFetching && isDisabled) {
            // Trigger the submit action when exact match reading goes from true to false
            // and the typeahead is disabled.
            this.onSearchInputSubmitAction(typeaheadInputValue);
        }
    }

    onSearchInputSubmit(term) {
        const { hasActiveRecord } = this.state;

        if (hasActiveRecord) {
            this.dispatchActiveRecord(term);
            return;
        }

        if (
            !hasExactMatchReadingFinished(
                this.props,
                term,
                this.config.global_search_config
                    .exact_match_request_criterion_regex,
                this.config.global_search_config.exact_match_enabled
            )
        ) {
            // Disable typeahead input controls. Hide dropdown. Go in lockdown state.
            this.setState({
                dropdownVisible: false,
                isDisabled: true,
            });
            return;
        }

        this.onSearchInputSubmitAction(term);
    }

    onSearchInputSubmitAction(term) {
        const {
            helpers: { dispatch },
        } = this.props;
        this.setState({ dropdownVisible: false, isDisabled: false });
        if (isExactMatchPresent(this.props, term)) {
            const sysId = get(this.props, 'exactMatch.sys_id');
            const table = get(this.props, 'exactMatch.table');

            GlobalSearchUtils.dispatchItemSelected(dispatch, {
                sys_id: sysId,
                table,
            });
            return;
        }

        if (term) {
            GlobalSearchUtils.dispatchTermSearched(dispatch, term);
        }
    }

    onSearchInputEscapeKeyDown() {
        this.setState({ dropdownVisible: false });
        this.onActiveRecordUpdate(false);
    }

    onTypeaheadRecordClick(record) {
        const {
            helpers: { dispatch },
        } = this.props;

        return () => {
            const recordType = typeof record === 'string' ? 'term' : 'result';
            if (recordType === 'term') {
                GlobalSearchUtils.dispatchTermSearched(dispatch, record);
                this.setState({
                    dropdownVisible: false,
                    typeaheadInputValue: record,
                    searchExpanded: true,
                });
            }

            if (recordType === 'result') {
                GlobalSearchUtils.dispatchItemSelected(dispatch, record);
                this.resetTypeaheadState(false);
            }
        };
    }

    // this may not be the best way of achieving this since we are passing down an action rather than a static attribute/prop ... but it helps to keep the component logic separated and the code to be sanely maintainable
    onActiveRecordUpdate(activeRecord, ariaSelected) {
        if (activeRecord) {
            this.setState({
                navigateInDirection: '',
                hasActiveRecord: true,
                activeRecord,
                ariaSelected,
            });
        } else {
            this.setState({
                navigateInDirection: '',
                hasActiveRecord: false,
                activeRecord: {},
                ariaSelected,
            });
        }
    }

    get currentMode() {
        const { typeaheadInputValue } = this.state;

        if (trim(typeaheadInputValue) === '') {
            return this.constructor.MODES.RECENT_ACTIONS;
        }

        return this.constructor.MODES.EXACT_MATCH;
    }

    getResolvedConfig() {
        const {
            workspaceConfigs,
            properties: { isLegacyWorkspace },
        } = this.props;

        if (!isLegacyWorkspace && !workspaceConfigs) {
            return {};
        }

        if (!isLegacyWorkspace && workspaceConfigs) {
            return {
                global_search_config: {
                    ...workspaceConfigs.viewConfig,
                    ...workspaceConfigs.exactMatchPreference,
                },
            };
        }

        const prefetchedConfig = getGlobalSearchConfigPresourceData(this.props);
        const userPref = getUserPreferencePresourceData(this.props);

        return {
            global_search_config: {
                ...prefetchedConfig,
                ...userPref,
            },
        };
    }

    get config() {
        const resolvedConfig = merge(
            {},
            {
                global_search_config: PresourceDefaults[GLOBAL_SEARCH_CONFIG],
                search_sources: [],
            },
            this.getResolvedConfig(),
            configFromSysProperties()
        );
        return resolvedConfig;
    }

    setNavigationDirection(up) {
        return () => {
            const navigateInDirection = up ? 'up' : 'down';
            this.setState({ navigateInDirection });
        };
    }

    handleBlur = () => {
        const { typeaheadInputValue } = this.state;
        if (typeaheadInputValue.length === 0) {
            this.setState({
                dropdownVisible: false,
                searchExpanded: false,
            });
        } else {
            this.setState({
                dropdownVisible: false,
                searchExpanded: true,
            });
        }
    };

    handleFocus = () => {
        const {
            helpers: { dispatch },
        } = this.props;

        this.setState({
            dropdownVisible: true,
            searchExpanded: true,
        });

        dispatch(USER_PREFERENCES_FETCH, { name: USER_PREFERENCES_NAME });
    };

    handleChange = value => {
        const {
            helpers: { dispatch },
        } = this.props;

        this.clearActiveRecord();
        this.setState({ typeaheadInputValue: value });
        dispatch(FIT_RESIZE);
    };

    handleSearchIconClick = () => {
        const { searchExpanded } = this.state;
        this.setState({
            searchExpanded: !searchExpanded,
            dropdownVisible: !searchExpanded,
        });
    };

    handleClearIconClick = () => {
        this.setState({
            typeaheadInputValue: '',
            dropdownVisible: true,
        });
    };

    handleGeneralKeyDown = () => {
        this.setState({ dropdownVisible: true });
    };

    clearActiveRecord() {
        const { ariaSelected } = this.state;
        this.onActiveRecordUpdate(false, ariaSelected);
    }

    dispatchActiveRecord(term = '') {
        const { activeRecord } = this.state;
        const {
            helpers: { dispatch },
        } = this.props;

        if (activeRecord.type === TypeaheadRecord.TYPES.TERM) {
            GlobalSearchUtils.dispatchTermSearched(dispatch, activeRecord.term);
            this.setState({
                dropdownVisible: false,
                typeaheadInputValue: activeRecord.term,
                searchExpanded: true,
            });
        } else if (activeRecord.type === TypeaheadRecord.TYPES.RESULT) {
            GlobalSearchUtils.dispatchItemSelected(
                dispatch,
                activeRecord.item,
                activeRecord.sourceId
            );
            this.resetTypeaheadState(false);
        } else if (
            activeRecord.type === TypeaheadRecord.TYPES.SEARCH_ALL_ACTION
        ) {
            GlobalSearchUtils.dispatchTermSearched(dispatch, term);
            this.setState({ dropdownVisible: false });
        }
        this.setState({ hasActiveRecord: false, activeRecord: {} });
    }

    resetTypeaheadState(dropdownVisible) {
        this.setState(
            Object.assign({}, this.constructor.DEFAULT_LOCAL_PROPS, {
                dropdownVisible,
            })
        );
    }

    render() {
        const {
            ariaSelected,
            dropdownVisible,
            hasActiveRecord,
            navigateInDirection,
            typeaheadInputValue,
            searchExpanded,
            isDisabled,
        } = this.state;
        const {
            exactMatch,
            globalSearchAction,
            sysId,
            table,
            item,
            userPreferences,
            properties: { isLegacyWorkspace },
        } = this.props;

        const otherSeismicProps = omit(this.props, ['channels', 'instanceId']);
        const seismicProps = {
            ...otherSeismicProps,
            ...globalSearchAction,
        };

        const isRecentActionsVisible =
            dropdownVisible &&
            this.currentMode === this.constructor.MODES.RECENT_ACTIONS &&
            userPreferences &&
            userPreferences.typeaheadRecentActionsModel &&
            userPreferences.typeaheadRecentActionsModel.size > 0;

        const isExactMatchResultVisible =
            dropdownVisible &&
            this.currentMode === this.constructor.MODES.EXACT_MATCH;

        // temporary workaround for seismic issue
        if (sysId !== this.sysId) {
            const itemSelected = {
                sys_id: sysId,
                table,
                item,
                action: 'ITEM_SELECTED',
                actionMetadata: {
                    sourceId: '',
                },
            };

            this.sysId = sysId;
            Object.assign(seismicProps, itemSelected);
        }

        return (
            <ConfigProvider config={this.config}>
                <div className="sn-global-typeahead">
                    <ErrorBoundary>
                        <ComponentTypeaheadInput
                            {...seismicProps}
                            exactMatch={exactMatch}
                            onUpArrowKeyDown={this.setNavigationDirection(true)}
                            onDownArrowKeyDown={this.setNavigationDirection(
                                false
                            )}
                            onInputSubmit={this.onSearchInputSubmit}
                            onEscapeKeyDown={this.onSearchInputEscapeKeyDown}
                            handleBlur={this.handleBlur}
                            handleChange={this.handleChange}
                            handleFocus={this.handleFocus}
                            handleSearchIconClick={this.handleSearchIconClick}
                            handleClearIconClick={this.handleClearIconClick}
                            handleGeneralKeyDown={this.handleGeneralKeyDown}
                            ariaSelected={ariaSelected}
                            typeaheadInputValue={typeaheadInputValue}
                            searchExpanded={searchExpanded}
                            dropdownVisible={dropdownVisible}
                            isDisabled={isDisabled}
                            isLegacyWorkspace={isLegacyWorkspace}
                        />
                    </ErrorBoundary>
                    <ErrorBoundary>
                        <ComponentExactMatch
                            typeaheadInputValue={typeaheadInputValue}
                            {...seismicProps}
                            data={exactMatch}
                            navigateInDirection={navigateInDirection}
                            hasActiveRecord={hasActiveRecord}
                            onActiveRecordUpdate={this.onActiveRecordUpdate}
                            onTypeaheadRecordClick={this.onTypeaheadRecordClick}
                            isVisible={!!isExactMatchResultVisible}
                        />
                    </ErrorBoundary>
                    <ErrorBoundary>
                        <ComponentRecentActions
                            className={
                                isRecentActionsVisible ? undefined : 'hidden'
                            }
                            {...seismicProps}
                            userPreferences={userPreferences}
                            navigateInDirection={navigateInDirection}
                            hasActiveRecord={hasActiveRecord}
                            onActiveRecordUpdate={this.onActiveRecordUpdate}
                            onTypeaheadRecordClick={this.onTypeaheadRecordClick}
                            isVisible={isRecentActionsVisible}
                        />
                    </ErrorBoundary>
                </div>
            </ConfigProvider>
        );
    }
}

/* eslint-disable react/forbid-prop-types */
ComponentWorkspaceGlobalSearchTypeahead.propTypes = {
    exactMatch: exactMatchDataShape,
    properties: PropTypes.shape({
        globalSearchViewConfigId: PropTypes.string,
        isLegacyWorkspace: PropTypes.bool,
    }),
    globalSearchAction: PropTypes.object,
    helpers: PropTypes.func.isRequired,
    userPreferences: userPreferenceDataShape,
    sysId: PropTypes.string,
    table: PropTypes.string,
    item: PropTypes.object,
    workspaceConfigs: PropTypes.shape({
        viewConfig: PropTypes.object,
        exactMatchPreference: PropTypes.object,
    }),
};

ComponentWorkspaceGlobalSearchTypeahead.defaultProps = {
    globalSearchAction: undefined,
    item: undefined,
    sysId: undefined,
    table: undefined,
    userPreferences: undefined,
    exactMatch: undefined,
    properties: {
        globalSearchViewConfigId: undefined,
        isLegacyWorkspace: undefined,
    },
    workspaceConfigs: undefined,
};

ComponentWorkspaceGlobalSearchTypeahead.style = `${style} ${iconStyle}`;

export default ComponentWorkspaceGlobalSearchTypeahead;
