import '@servicenow/now-label-value';
import filter from 'lodash/filter';
import get from 'lodash/get';
import keys from 'lodash/keys';
import pick from 'lodash/pick';
import take from 'lodash/take';
import trim from 'lodash/trim';
import PropTypes from 'prop-types';
import React from 'react';
import { t } from 'sn-translate';
import InvisibleContent from '../../common/InvisibleContent';
import getAccessibleDisplayValueMessage from '../../utils/getAccessibleDisplayValueMessage';

export const MAX_FIELDS_ALLOWED = 4;

const ResultRecordField = ({ field }) => (
    <span className="sn-list-group--content">
        <span>{field}</span>
    </span>
);

const getDisplayValue = field => {
    const displayValue = field.displayValue || field.display;
    return displayValue && trim(displayValue).length ? displayValue : null;
};

const getStackedValueItems = (displayFields, record) =>
    displayFields.reduce((accumlated, label) => {
        const value = getDisplayValue(record.data[label]) || t('None');
        return [...accumlated, { label, value: { type: 'string', value } }];
    }, []);

const ResultRecord = ({ record, displayFields }) => {
    const fields = keys(pick(record.data, displayFields));
    const rawTitle = trim(get(record, 'metadata.title', ''));
    const recordTitle = rawTitle || t('No Title');

    const orderedDisplayFields = take(
        filter(fields, field => field !== 'sys_id'),
        MAX_FIELDS_ALLOWED
    );

    const stackedValueItems = getStackedValueItems(
        orderedDisplayFields,
        record
    );

    const serializedRecordValueId = `serializedRecordValue_${record.sys_id}`;

    return (
        <div
            aria-describedby={serializedRecordValueId}
            className="sncwsgsta-record"
        >
            <span className="sn-list-group--item--title" aria-hidden>
                <span>{recordTitle}</span>
            </span>
            <now-label-value-stacked
                align="horizontal-equal"
                truncated
                items={stackedValueItems}
                size="sm"
                aria-hidden
            />
            <InvisibleContent id={serializedRecordValueId}>
                {getAccessibleDisplayValueMessage(
                    recordTitle,
                    stackedValueItems
                )}
            </InvisibleContent>
        </div>
    );
};

ResultRecord.propTypes = {
    record: PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
    displayFields: PropTypes.array.isRequired, // eslint-disable-line react/forbid-prop-types
};

ResultRecordField.propTypes = {
    field: PropTypes.string.isRequired,
};

export default ResultRecord;
