import PropTypes from 'prop-types';
import React from 'react';
import { t } from 'sn-translate';
import { KEY_CODES } from '../../utils/GlobalSearchUtils';
import Icon from './Icon';
import AriaLiveRegion from '../../common/AriaLiveRegion';

export const SEARCH_ICONS = {
    SEARCH: 'magnifyingGlassOutline',
    CLEAR: 'closeOutline',
};

class SearchInput extends React.PureComponent {
    constructor(props) {
        super(props);

        this.onChange = this.onChange.bind(this);
        this.onKeyDown = this.onKeyDown.bind(this);
        this.onKeyPress = this.onKeyPress.bind(this);
    }

    static get ICONS() {
        return SEARCH_ICONS;
    }

    onChange(event) {
        const { onChange } = this.props;
        const inputValue = event.target.value;

        onChange(inputValue);
    }

    onKeyDown(event) {
        const {
            onEscapeKeyDown,
            onGeneralKeyDown,
            onDownArrowKeyDown,
            onUpArrowKeyDown,
        } = this.props;

        if (event.which === KEY_CODES.ESCAPE) {
            onEscapeKeyDown(event);
        } else if (event.which === KEY_CODES.DOWN_ARROW) {
            event.preventDefault();
            onDownArrowKeyDown(event);
        } else if (event.which === KEY_CODES.UP_ARROW) {
            event.preventDefault();
            onUpArrowKeyDown(event);
        }

        if (
            event.which !== KEY_CODES.ESCAPE &&
            event.which !== KEY_CODES.RETURN
        ) {
            onGeneralKeyDown(event);
        }
    }

    onKeyPress(event) {
        const { onSubmit, value } = this.props;
        if (event.which === KEY_CODES.RETURN) {
            onSubmit(value);
        }
    }

    render() {
        const {
            value,
            isDropdownVisible,
            isExpanded,
            isDisabled,
            bindInputRef,
            onBlur,
            onFocus,
            onClearIconClick,
            onSearchIconClick,
            ariaActiveDescendant,
            ariaLiveText,
            isLegacyWorkspace,
        } = this.props;

        // for UIB workspaces, we want different padding around the search button
        const searchButtonClassName =
            !isLegacyWorkspace && !isExpanded
                ? 'sn-global-typeahead-btn -search -uib'
                : 'sn-global-typeahead-btn -search';
        const hasText = typeof value === 'string' && value.length > 0;

        return (
            <div
                className="sn-global-typeahead-control-container"
                role="search"
            >
                <Icon
                    ariaExpanded={isDropdownVisible}
                    iconSize="lg"
                    onClick={onSearchIconClick}
                    buttonClassName={
                        isExpanded
                            ? `${searchButtonClassName} is-focus is-selected`
                            : searchButtonClassName
                    }
                    setRef={ref => {
                        this.searchIconRef = ref;
                    }}
                    iconClassName={SearchInput.ICONS.SEARCH}
                    tooltipText={t('Search')}
                    ariaLabel={t('Search')}
                />
                <div className="sn-global-typeahead-control">
                    <AriaLiveRegion message={ariaLiveText} />
                    <input
                        className="sn-global-typeahead-input"
                        type="text"
                        name="sncwsgs-typeahead-input"
                        id="sncwsgs-typeahead-input"
                        placeholder={t('Search Workspace')}
                        autoComplete="off"
                        aria-label={t('Search Workspace')}
                        aria-hidden={!isExpanded}
                        aria-expanded={isExpanded}
                        aria-activedescendant={ariaActiveDescendant}
                        aria-autocomplete="both"
                        aria-owns="sncwsgs-typeahead-sections"
                        aria-controls="sncwsgs-typeahead-sections"
                        aria-describedby="sncwsgs-typeahead-instructions"
                        aria-haspopup="listbox"
                        ref={bindInputRef}
                        onChange={this.onChange}
                        onKeyPress={this.onKeyPress}
                        onKeyDown={this.onKeyDown}
                        onBlur={onBlur}
                        onFocus={onFocus}
                        defaultValue={value}
                        disabled={isDisabled}
                        role="combobox"
                    />
                    {isExpanded && hasText && (
                        <Icon
                            iconSize="sm"
                            onClick={onClearIconClick}
                            buttonClassName="sn-global-typeahead-btn -clear"
                            iconClassName={SearchInput.ICONS.CLEAR}
                            ariaLabel={t('Clear')}
                        />
                    )}
                </div>
                <now-tooltip
                    target-ref={this.searchIconRef}
                    content={t('Search')}
                />
            </div>
        );
    }
}

SearchInput.propTypes = {
    onFocus: PropTypes.func.isRequired,
    onBlur: PropTypes.func.isRequired,
    onSubmit: PropTypes.func.isRequired,
    onChange: PropTypes.func.isRequired,
    onDownArrowKeyDown: PropTypes.func.isRequired,
    onUpArrowKeyDown: PropTypes.func.isRequired,
    onEscapeKeyDown: PropTypes.func.isRequired,
    onSearchIconClick: PropTypes.func.isRequired,
    onClearIconClick: PropTypes.func.isRequired,
    onGeneralKeyDown: PropTypes.func.isRequired,
    bindInputRef: PropTypes.func.isRequired,
    value: PropTypes.string,
    isExpanded: PropTypes.bool,
    isDropdownVisible: PropTypes.bool,
    isDisabled: PropTypes.bool,
    ariaActiveDescendant: PropTypes.string,
    ariaLiveText: PropTypes.string,
    isLegacyWorkspace: PropTypes.bool,
};

SearchInput.defaultProps = {
    value: '',
    isExpanded: false,
    isDropdownVisible: false,
    isDisabled: false,
    ariaActiveDescendant: '',
    ariaLiveText: '',
    isLegacyWorkspace: undefined,
};

export default SearchInput;
