import {
    TYPEAHEAD_ITEM_LOOKUP,
    TYPEAHEAD_ITEM_LOOKUP_FAILED,
    TYPEAHEAD_ITEM_LOOKUP_STARTED,
    TYPEAHEAD_ITEM_LOOKUP_SUCCEEDED,
} from './actions';
import {
    handleTypeaheadItemLookup,
    handleTypeaheadItemLookupFailed,
    handleTypeaheadItemLookupStarted,
    handleTypeaheadItemLookupSucceeded,
} from './effects';

export default {
    [TYPEAHEAD_ITEM_LOOKUP]: {
        ...handleTypeaheadItemLookup,
        stopPropagation: true,
    },
    [TYPEAHEAD_ITEM_LOOKUP_FAILED]: {
        effect: handleTypeaheadItemLookupFailed,
        stopPropagation: true,
    },
    [TYPEAHEAD_ITEM_LOOKUP_STARTED]: {
        effect: handleTypeaheadItemLookupStarted,
        stopPropagation: true,
    },
    [TYPEAHEAD_ITEM_LOOKUP_SUCCEEDED]: {
        effect: handleTypeaheadItemLookupSucceeded,
        stopPropagation: true,
    },
};
