import get from 'lodash/get';
import { t } from 'sn-translate';
import parseUserPreferenceValue from './utils/parseUserPreferenceValue';
import { USER_PREFERENCES_SET } from '../../actions';
import { createLog } from '../../../utils/Logger';
import RecentActionsModel from '../../../utils/RecentActionsModel';
import getBehaviorProp from '../../../behaviors/util/getBehaviorProp';
import { GLOBAL_SEARCH_CONFIG } from '../../../behaviors/constants';

const LOG = createLog('handleTabUserPreferenceFetchSucceeded');

const addRecentAction = (dispatch, model, type, actionData, metadata) => {
    const stringifiedRecentActions = model.addRecentAction(
        type,
        actionData,
        metadata
    );

    dispatch(USER_PREFERENCES_SET, {
        name: 'workspace.globalSearch.recentActions',
        value: stringifiedRecentActions,
    });
};

const getGlobalConfigs = state => {
    if (state.workspaceConfigs) {
        return state.workspaceConfigs.viewConfig;
    }

    return getBehaviorProp(state, GLOBAL_SEARCH_CONFIG);
};

const handleTabUserPreferenceFetchSucceeded = coeffects => {
    const { dispatch, state, action } = coeffects;
    const { errors } = action.payload;
    if (errors && errors.length > 0) {
        errors.forEach(e => LOG.error(e.message));
        return;
    }

    const {
        payload: { data: userPrefData },
        meta: {
            options: {
                variables: { data, sourceId },
            },
        },
    } = action;

    const userPreferencesQuery = get(userPrefData, 'GlideDomain_Query.user');
    const userPreferences = userPreferencesQuery
        ? {
              ...state.userPreferences,
              ...userPreferencesQuery.preferences[0],
          }
        : { ...state.userPreferences };

    const {
        recent_items_count: recentItemsCount,
        recent_terms_count: recentTermsCount,
        recent_ttl_in_days: recentTTLInDays,
    } = getGlobalConfigs(state);

    const typeaheadRecentActionsModel = new RecentActionsModel(
        parseUserPreferenceValue(userPreferences),
        recentItemsCount,
        recentTermsCount,
        recentTTLInDays,
        t('Recently Searched'),
        t('Recently Viewed')
    );

    addRecentAction(
        dispatch,
        typeaheadRecentActionsModel,
        RecentActionsModel.SECTIONS.ITEM_SELECTED,
        data,
        { sourceId }
    );
};
export default handleTabUserPreferenceFetchSucceeded;
