import { t } from 'sn-translate';
import GlobalSearchModel from './GlobalSearchModel';

class ExactMatchModel extends GlobalSearchModel {
    constructor(response) {
        super();
        this.parse(response);
    }

    parse(response) {
        if (response) {
            const { term, record, displayFields } = response;

            this.term = term;
            this.sources = [];
            this.resultCount = 1;

            if (record && displayFields) {
                this.resultSectionsRecordLength.push(1);
                this.sources.push({
                    sys_id: 'EXACT_MATCH_SECTION',
                });
                this.mapTableNameToSourceId.EXACT_MATCH_SECTION = 'Exact Match';
                this.mapSourceIdToSourceData.EXACT_MATCH_SECTION = {
                    name: t('Exact Match'),
                    record_count: 1,
                    records: [record],
                    fields: displayFields,
                };
            }
        }

        // this is to account for Search All Records
        this.resultSectionsRecordLength.push(1);
        this.totalResults = this.resultSectionsRecordLength.reduce(
            (count, value) => count + value,
            0
        );
    }
}

export default ExactMatchModel;
